
// M phng API - trong ng dng thc t, nhng hm ny s gi n API thc

// URL c s - trong d n thc t s l URL API thc ca bn
const BASE_URL = 'https://api.hivtreatment.example.com/api/v1';

// Hm tin ch  x l cc yu cu API
const fetchAPI = async (endpoint, options = {}) => {
  try {
    // Trong ng dng thc t, ly token t localStorage hoc authContext
    const token = localStorage.getItem('hivAppToken');
    
    const defaultHeaders = {
      'Content-Type': 'application/json',
    };
    
    // Thm token xc thc nu c
    if (token) {
      defaultHeaders['Authorization'] = `Bearer ${token}`;
    }
    
    const response = await fetch(`${BASE_URL}${endpoint}`, {
      ...options,
      headers: {
        ...defaultHeaders,
        ...options.headers,
      },
    });
    
    const data = await response.json();
    
    if (!response.ok) {
      // X l li t API
      throw new Error(data.message || ' xy ra li khi gi API');
    }
    
    return data;
  } catch (error) {
    console.error('API Error:', error);
    throw error;
  }
};

// Cc hm x l API xc thc
export const authAPI = {
  login: async (credentials) => {
    // M phng - trong d n thc t s gi API thc
    return new Promise((resolve) => {
      setTimeout(() => {
        if (credentials.email && credentials.password) {
          const user = {
            id: 'user123',
            name: 'Nguyn Vn A',
            email: credentials.email,
            token: 'mock_token_' + Math.random(),
          };
          resolve({ success: true, data: user });
        } else {
          resolve({ success: false, message: 'Email hoc mt khu khng ng' });
        }
      }, 800);
    });
    
    // Trong d n thc t
    // return fetchAPI('/auth/login', {
    //   method: 'POST',
    //   body: JSON.stringify(credentials),
    // });
  },
  
  register: async (userData) => {
    // M phng - trong d n thc t s gi API thc
    return new Promise((resolve) => {
      setTimeout(() => {
        if (userData.name && userData.email && userData.password) {
          const user = {
            id: 'user' + Math.floor(Math.random() * 1000),
            name: userData.name,
            email: userData.email,
            token: 'mock_token_' + Math.random(),
          };
          resolve({ success: true, data: user });
        } else {
          resolve({ success: false, message: 'Vui lng in y  thng tin' });
        }
      }, 800);
    });
    
    // Trong d n thc t
    // return fetchAPI('/auth/register', {
    //   method: 'POST',
    //   body: JSON.stringify(userData),
    // });
  },
  
  resetPassword: async (email) => {
    // M phng - trong d n thc t s gi API thc
    return new Promise((resolve) => {
      setTimeout(() => {
        if (email) {
          resolve({ 
            success: true, 
            message: 'Hng dn t li mt khu  c gi n email ca bn' 
          });
        } else {
          resolve({ success: false, message: 'Vui lng nhp a ch email' });
        }
      }, 800);
    });
    
    // Trong d n thc t
    // return fetchAPI('/auth/reset-password', {
    //   method: 'POST',
    //   body: JSON.stringify({ email }),
    // });
  },
};

