import React, { useState, useEffect, useContext } from 'react';
import { Link } from 'react-router-dom';
import { AuthContext } from '../context/AuthContext';
import { apiService } from '../services/apiService';
import { formatDate, analyzeCD4Trend, analyzeViralLoadTrend } from '../utils/helpers';
import '../styles/TreatmentPlan.css';

const TreatmentPlan = () => {
  const { currentUser } = useContext(AuthContext);
  const [treatmentPlan, setTreatmentPlan] = useState(null);
  const [labResults, setLabResults] = useState([]);
  const [medications, setMedications] = useState([]);
  const [loading, setLoading] = useState(true);
  const [activeTab, setActiveTab] = useState('overview');
  const [showPrintPreview, setShowPrintPreview] = useState(false);

  useEffect(() => {
    const fetchTreatmentData = async () => {
      try {
        setLoading(true);
        
        // Fetch treatment plan data
        const planData = await apiService.getTreatmentPlan(currentUser.id);
        setTreatmentPlan(planData);
        
        // Fetch lab results
        const labData = await apiService.getLabResults(currentUser.id);
        setLabResults(labData);
        
        // Fetch medications
        const medData = await apiService.getMedications(currentUser.id);
        setMedications(medData);
        
        setLoading(false);
      } catch (error) {
        console.error('Error fetching treatment data:', error);
        setLoading(false);
      }
    };

    if (currentUser) {
      fetchTreatmentData();
    }
  }, [currentUser]);

  // Calculate CD4 and viral load trends
  const cd4Trend = labResults.length >= 2 ? analyzeCD4Trend(labResults) : null;
  const viralLoadTrend = labResults.length >= 2 ? analyzeViralLoadTrend(labResults) : null;

  // Sort lab results by date (newest first)
  const sortedLabResults = [...labResults].sort((a, b) => new Date(b.date) - new Date(a.date));
  
  // Get latest lab results
  const latestLabResults = sortedLabResults.length > 0 ? sortedLabResults[0] : null;

  // Format medication schedule
  const formatSchedule = (schedule) => {
    if (!schedule || !schedule.length) return 'N/A';
    return schedule.join(', ');
  };

  // Toggle print preview
  const handlePrint = () => {
    setShowPrintPreview(true);
    setTimeout(() => {
      window.print();
      setShowPrintPreview(false);
    }, 300);
  };

  if (loading) {
    return (
      <div className="treatment-plan-page">
        <div className="loading-container">
          <div className="spinner"></div>
          <p>ang ti thng tin k hoch iu tr...</p>
        </div>
      </div>
    );
  }

  if (!treatmentPlan) {
    return (
      <div className="treatment-plan-page">
        <div className="no-plan-container">
          <i className="fas fa-clipboard-list fa-4x"></i>
          <h2>Cha c k hoch iu tr</h2>
          <p>Bn cha c k hoch iu tr no. Vui lng lin h vi bc s  c t vn.</p>
          <Link to="/appointment" className="btn btn-primary">t lch hn</Link>
        </div>
      </div>
    );
  }

  return (
    <div className={`treatment-plan-page ${showPrintPreview ? 'print-preview' : ''}`}>
      {/* Header Section */}
      <section className="treatment-header">
        <div className="container">
          <div className="treatment-header-content">
            <h1>K Hoch iu Tr HIV</h1>
            <p>Theo di v qun l k hoch iu tr HIV hiu qu</p>
          </div>
        </div>
      </section>

      {/* Main Content */}
      <section className="treatment-content">
        <div className="container">
          <div className="plan-container">
            {/* Top Action Bar */}
            <div className="plan-actions">
              <h2>K Hoch iu Tr Ca {currentUser.firstName}</h2>
              <div className="action-buttons">
                <button className="action-btn print-btn" onClick={handlePrint}>
                  <i className="fas fa-print"></i> In k hoch
                </button>
                <Link to="/appointment" className="action-btn appointment-btn">
                  <i className="fas fa-calendar-alt"></i> t lch hn
                </Link>
              </div>
            </div>

            {/* Plan Summary */}
            <div className="plan-summary">
              <div className="summary-item">
                <div className="summary-icon doctor-icon">
                  <i className="fas fa-user-md"></i>
                </div>
                <div className="summary-details">
                  <h3>Bc s iu tr</h3>
                  <p>{treatmentPlan.doctor.name}</p>
                  <span>{treatmentPlan.doctor.specialty}</span>
                </div>
              </div>
              
              <div className="summary-item">
                <div className="summary-icon regimen-icon">
                  <i className="fas fa-pills"></i>
                </div>
                <div className="summary-details">
                  <h3>Phc  iu tr</h3>
                  <p>{treatmentPlan.regimenName}</p>
                  <span>p dng t {formatDate(treatmentPlan.startDate)}</span>
                </div>
              </div>
              
              <div className="summary-item">
                <div className="summary-icon status-icon">
                  <i className={`fas fa-${treatmentPlan.status === 'active' ? 'check-circle' : 'exclamation-circle'}`}></i>
                </div>
                <div className="summary-details">
                  <h3>Trng thi</h3>
                  <p className={`status-${treatmentPlan.status}`}>
                    {treatmentPlan.status === 'active' ? 'ang iu tr' : 'Tm ngng'}
                  </p>
                  <span>Cp nht: {formatDate(treatmentPlan.updatedAt)}</span>
                </div>
              </div>
            </div>

            {/* Tab Navigation */}
            <div className="plan-tabs">
              <button 
                className={`tab-btn ${activeTab === 'overview' ? 'active' : ''}`}
                onClick={() => setActiveTab('overview')}
              >
                <i className="fas fa-home"></i> Tng quan
              </button>
              <button 
                className={`tab-btn ${activeTab === 'medications' ? 'active' : ''}`}
                onClick={() => setActiveTab('medications')}
              >
                <i className="fas fa-pills"></i> Thuc iu tr
              </button>
              <button 
                className={`tab-btn ${activeTab === 'labResults' ? 'active' : ''}`}
                onClick={() => setActiveTab('labResults')}
              >
                <i className="fas fa-flask"></i> Kt qu xt nghim
              </button>
              <button 
                className={`tab-btn ${activeTab === 'instructions' ? 'active' : ''}`}
                onClick={() => setActiveTab('instructions')}
              >
                <i className="fas fa-list-ul"></i> Hng dn
              </button>
              <button 
                className={`tab-btn ${activeTab === 'appointments' ? 'active' : ''}`}
                onClick={() => setActiveTab('appointments')}
              >
                <i className="fas fa-calendar-check"></i> Lch hn
              </button>
            </div>

            {/* Tab Content */}
            <div className="tab-content">
              {/* Overview Tab */}
              {activeTab === 'overview' && (
                <div className="overview-tab">
                  <div className="overview-grid">
                    <div className="overview-card health-indicators">
                      <h3><i className="fas fa-heartbeat"></i> Ch s sc khe gn y</h3>
                      {latestLabResults ? (
                        <div className="indicators-grid">
                          <div className="indicator">
                            <h4>CD4</h4>
                            <div className="indicator-value">
                              {latestLabResults.cd4Count}
                              <span>t bo/mm</span>
                            </div>
                            {cd4Trend && (
                              <div className={`trend-badge ${cd4Trend.direction}`}>
                                <i className={`fas fa-arrow-${cd4Trend.direction === 'increasing' ? 'up' : cd4Trend.direction === 'decreasing' ? 'down' : 'right'}`}></i>
                                {cd4Trend.percentage}%
                              </div>
                            )}
                          </div>
                          
                          <div className="indicator">
                            <h4>Ti lng vi-rt</h4>
                            <div className="indicator-value">
                              {latestLabResults.viralLoad}
                              <span>bn sao/ml</span>
                            </div>
                            {viralLoadTrend && (
                              <div className={`trend-badge ${viralLoadTrend.direction === 'decreasing' ? 'positive' : viralLoadTrend.direction === 'increasing' ? 'negative' : 'neutral'}`}>
                                <i className={`fas fa-arrow-${viralLoadTrend.direction === 'decreasing' ? 'down' : viralLoadTrend.direction === 'increasing' ? 'up' : 'right'}`}></i>
                                {viralLoadTrend.percentage}%
                              </div>
                            )}
                          </div>
                          
                          <div className="indicator">
                            <h4>Ngy xt nghim</h4>
                            <div className="indicator-date">
                              {formatDate(latestLabResults.date)}
                            </div>
                          </div>
                        </div>
                      ) : (
                        <div className="no-data-message">
                          <p>Cha c d liu xt nghim. Vui lng lin h vi bc s  c t vn.</p>
                        </div>
                      )}
                    </div>
                    
                    <div className="overview-card next-steps">
                      <h3><i className="fas fa-tasks"></i> Cc bc tip theo</h3>
                      <ul className="steps-list">
                        {treatmentPlan.nextSteps.map((step, index) => (
                          <li key={index}>
                            <div className={`step-status ${step.completed ? 'completed' : 'pending'}`}>
                              {step.completed ? (
                                <i className="fas fa-check-circle"></i>
                              ) : (
                                <span className="step-number">{index + 1}</span>
                              )}
                            </div>
                            <div className="step-details">
                              <h4>{step.title}</h4>
                              <p>{step.description}</p>
                              {step.dueDate && (
                                <span className="step-date">Hn: {formatDate(step.dueDate)}</span>
                              )}
                            </div>
                          </li>
                        ))}
                      </ul>
                    </div>
                  </div>
                  
                  <div className="overview-section treatment-goals">
                    <h3><i className="fas fa-bullseye"></i> Mc tiu iu tr</h3>
                    <div className="goals-container">
                      {treatmentPlan.goals.map((goal, index) => (
                        <div key={index} className="goal-card">
                          <div className="goal-icon">
                            <i className={`fas fa-${goal.icon}`}></i>
                          </div>
                          <h4>{goal.title}</h4>
                          <p>{goal.description}</p>
                        </div>
                      ))}
                    </div>
                  </div>
                  
                  <div className="overview-section doctor-notes">
                    <h3><i className="fas fa-comment-medical"></i> Ghi ch ca bc s</h3>
                    <div className="notes-container">
                      {treatmentPlan.doctorNotes ? (
                        <div className="note-content">
                          <p>{treatmentPlan.doctorNotes}</p>
                          <div className="note-meta">
                            <span className="note-doctor">{treatmentPlan.doctor.name}</span>
                            <span className="note-date">{formatDate(treatmentPlan.updatedAt)}</span>
                          </div>
                        </div>
                      ) : (
                        <div className="no-data-message">
                          <p>Khng c ghi ch no t bc s.</p>
                        </div>
                      )}
                    </div>
                  </div>
                </div>
              )}
              
              {/* Medications Tab */}
              {activeTab === 'medications' && (
                <div className="medications-tab">
                  <div className="medications-header">
                    <h3><i className="fas fa-prescription-bottle-alt"></i> Thuc iu tr hin ti</h3>
                    <p>Phc : <strong>{treatmentPlan.regimenName}</strong></p>
                  </div>
                  
                  {medications.length > 0 ? (
                    <div className="medications-list">
                      {medications.map(medication => (
                        <div key={medication.id} className="medication-card">
                          <div className="medication-header">
                            <h4>{medication.name}</h4>
                            <span className={`status-badge ${medication.active ? 'active' : 'inactive'}`}>
                              {medication.active ? 'ang dng' : ' ngng'}
                            </span>
                          </div>
                          
                          <div className="medication-details">
                            <div className="detail-item">
                              <i className="fas fa-pills"></i>
                              <div className="detail-content">
                                <span className="label">Liu lng:</span>
                                <span>{medication.dosage} {medication.unit}</span>
                              </div>
                            </div>
                            
                            <div className="detail-item">
                              <i className="fas fa-clock"></i>
                              <div className="detail-content">
                                <span className="label">Thi gian:</span>
                                <span>{formatSchedule(medication.schedule)}</span>
                              </div>
                            </div>
                            
                            <div className="detail-item">
                              <i className="fas fa-calendar-day"></i>
                              <div className="detail-content">
                                <span className="label">Tn sut:</span>
                                <span>{medication.frequency}</span>
                              </div>
                            </div>
                            
                            <div className="detail-item">
                              <i className="fas fa-utensils"></i>
                              <div className="detail-content">
                                <span className="label">Hng dn:</span>
                                <span>{medication.instructions || 'Khng c hng dn c bit'}</span>
                              </div>
                            </div>
                          </div>
                          
                          <div className="medication-notes">
                            <h5>Ghi ch:</h5>
                            <p>{medication.notes || 'Khng c ghi ch'}</p>
                          </div>
                          
                          <div className="medication-dates">
                            <div className="date-item">
                              <span className="label">Bt u:</span>
                              <span>{formatDate(medication.startDate)}</span>
                            </div>
                            {medication.endDate && (
                              <div className="date-item">
                                <span className="label">Kt thc:</span>
                                <span>{formatDate(medication.endDate)}</span>
                              </div>
                            )}
                          </div>
                          
                          <Link to={`/medications/${medication.id}`} className="medication-details-link">
                            Xem chi tit <i className="fas fa-chevron-right"></i>
                          </Link>
                        </div>
                      ))}
                    </div>
                  ) : (
                    <div className="no-data-message">
                      <p>Khng c thng tin v thuc iu tr. Vui lng lin h vi bc s  c t vn.</p>
                    </div>
                  )}
                  
                  <div className="medications-info">
                    <h4>Ti sao vic tun th iu tr rt quan trng?</h4>
                    <p>
                      Ung thuc ARV ng gi v u n s gip duy tr nng  thuc trong mu  mc hiu qu, 
                      c ch s nhn ln ca HIV, bo v h min dch v ngn nga khng thuc.
                    </p>
                    <Link to="/resources" className="link-with-arrow">
                      Tm hiu thm v thuc ARV <i className="fas fa-arrow-right"></i>
                    </Link>
                  </div>
                </div>
              )}
              
              {/* Lab Results Tab */}
              {activeTab === 'labResults' && (
                <div className="lab-results-tab">
                  <div className="lab-results-header">
                    <h3><i className="fas fa-flask"></i> Kt qu xt nghim</h3>
                    {sortedLabResults.length > 0 && (
                      <div className="lab-summary">
                        <div className={`summary-status ${viralLoadTrend?.status || 'unknown'}`}>
                          {viralLoadTrend?.status === 'suppressed' && ' kim sot vi-rt'}
                          {viralLoadTrend?.status === 'partially suppressed' && 'Kim sot vi-rt mt phn'}
                          {viralLoadTrend?.status === 'unsuppressed' && 'Cha kim sot vi-rt'}
                          {!viralLoadTrend?.status && 'Cha xc nh'}
                        </div>
                      </div>
                    )}
                  </div>
                  
                  {sortedLabResults.length > 0 ? (
                    <>
                      <div className="lab-charts">
                        <div className="chart-container">
                          <h4>CD4 (t bo/mm)</h4>
                          <div className="chart-placeholder cd4-chart">
                            {/* Chart would be implemented with a library like Chart.js */}
                            <div className="chart-note">Biu  CD4 s c hin th  y</div>
                          </div>
                        </div>
                        
                        <div className="chart-container">
                          <h4>Ti lng vi-rt (bn sao/ml)</h4>
                          <div className="chart-placeholder viral-load-chart">
                            {/* Chart would be implemented with a library like Chart.js */}
                            <div className="chart-note">Biu  ti lng vi-rt s c hin th  y</div>
                          </div>
                        </div>
                      </div>
                      
                      <div className="lab-results-table">
                        <h4>Lch s xt nghim</h4>
                        <div className="table-container">
                          <table>
                            <thead>
                              <tr>
                                <th>Ngy</th>
                                <th>CD4 (t bo/mm)</th>
                                <th>Ti lng vi-rt (bn sao/ml)</th>
                                <th>ALT (U/L)</th>
                                <th>AST (U/L)</th>
                                <th>Creatinine (mol/L)</th>
                                <th>Ghi ch</th>
                              </tr>
                            </thead>
                            <tbody>
                              {sortedLabResults.map((result, index) => (
                                <tr key={index}>
                                  <td>{formatDate(result.date)}</td>
                                  <td className={result.cd4Count < 200 ? 'warning' : result.cd4Count > 500 ? 'success' : ''}>
                                    {result.cd4Count}
                                  </td>
                                  <td className={result.viralLoad < 200 ? 'success' : result.viralLoad > 1000 ? 'warning' : ''}>
                                    {result.viralLoad}
                                  </td>
                                  <td className={result.alt > 40 ? 'warning' : ''}>{result.alt}</td>
                                  <td className={result.ast > 40 ? 'warning' : ''}>{result.ast}</td>
                                  <td className={result.creatinine > 115 ? 'warning' : ''}>{result.creatinine}</td>
                                  <td>{result.notes || ''}</td>
                                </tr>
                              ))}
                            </tbody>
                          </table>
                        </div>
                      </div>
                      
                      <div className="lab-interpretation">
                        <h4><i className="fas fa-info-circle"></i> Hiu v kt qu xt nghim ca bn</h4>
                        <div className="interpretation-grid">
                          <div className="interpretation-item">
                            <h5>CD4</h5>
                            <p>
                              T bo CD4 l mt loi t bo bch cu gip c th chng li nhim trng. 
                              Mc tiu l duy tr s lng CD4 trn 500 t bo/mm. 
                              S lng CD4 di 200 t bo/mm c coi l suy gim min dch nghim trng.
                            </p>
                          </div>
                          
                          <div className="interpretation-item">
                            <h5>Ti lng vi-rt</h5>
                            <p>
                              Ti lng vi-rt l s lng vi-rt HIV trong mu. 
                              Mc tiu iu tr l gim ti lng vi-rt n mc khng pht hin c (&lt;50 bn sao/ml). 
                              Ti lng vi-rt thp gim nguy c ly truyn v bo v h min dch.
                            </p>
                          </div>
                          
                          <div className="interpretation-item">
                            <h5>ALT v AST</h5>
                            <p>
                              ALT v AST l enzym gan, gip nh gi chc nng gan. 
                              Mt s thuc ARV c th nh hng n gan, v vy cn theo di cc ch s ny.
                            </p>
                          </div>
                          
                          <div className="interpretation-item">
                            <h5>Creatinine</h5>
                            <p>
                              Creatinine gip nh gi chc nng thn. 
                              Mt s thuc ARV c th nh hng n thn, v vy cn theo di ch s ny.
                            </p>
                          </div>
                        </div>
                      </div>
                    </>
                  ) : (
                    <div className="no-data-message">
                      <p>Cha c d liu xt nghim. Vui lng lin h vi bc s  c t vn.</p>
                    </div>
                  )}
                </div>
              )}
              
              {/* Instructions Tab */}
              {activeTab === 'instructions' && (
                <div className="instructions-tab">
                  <div className="instructions-header">
                    <h3><i className="fas fa-list-ul"></i> Hng dn iu tr</h3>
                  </div>
                  
                  <div className="instructions-grid">
                    <div className="instruction-card">
                      <div className="instruction-icon medication-icon">
                        <i className="fas fa-pills"></i>
                      </div>
                      <h4>Ung thuc</h4>
                      <ul>
                        <li>Ung thuc ng liu lng v thi gian quy nh</li>
                        <li>Khng b liu, ngay c khi cm thy khe</li>
                        <li>Nu qun ung thuc, hy ung ngay khi nh ra</li>
                        <li>Lu  tng tc thuc c th xy ra vi cc loi thuc khc</li>
                        <li>Lun mang theo thuc d phng khi i xa</li>
                      </ul>
                    </div>
                    
                    <div className="instruction-card">
                      <div className="instruction-icon monitoring-icon">
                        <i className="fas fa-stethoscope"></i>
                      </div>
                      <h4>Theo di sc khe</h4>
                      <ul>
                        <li>Khm nh k 3-6 thng/ln theo lch hn</li>
                        <li>Xt nghim CD4 v ti lng vi-rt nh k</li>
                        <li>Theo di cc tc dng ph c th xy ra</li>
                        <li>Bo co vi bc s v bt k triu chng mi no</li>
                        <li>Kim tra sc khe rng ming nh k</li>
                      </ul>
                    </div>
                    
                    <div className="instruction-card">
                      <div className="instruction-icon nutrition-icon">
                        <i className="fas fa-apple-alt"></i>
                      </div>
                      <h4>Dinh dng</h4>
                      <ul>
                        <li>Duy tr ch  n cn bng v a dng</li>
                        <li>Ung  nc (t nht 2 lt/ngy)</li>
                        <li>Hn ch ru bia v  ung c cn</li>
                        <li>n nhiu tri cy, rau xanh v protein</li>
                        <li>Tham kho  kin bc s v ch  n ph hp</li>
                      </ul>
                    </div>
                    
                    <div className="instruction-card">
                      <div className="instruction-icon lifestyle-icon">
                        <i className="fas fa-running"></i>
                      </div>
                      <h4>Li sng</h4>
                      <ul>
                        <li>Tp th dc u n (t nht 30 pht/ngy)</li>
                        <li>Trnh ht thuc l v cc cht kch thch</li>
                        <li>Duy tr gic ng  v cht lng</li>
                        <li>Qun l stress thng qua cc hot ng th gin</li>
                        <li>Tham gia cc nhm h tr nu cn</li>
                      </ul>
                    </div>
                    
                    <div className="instruction-card">
                      <div className="instruction-icon prevention-icon">
                        <i className="fas fa-shield-alt"></i>
                      </div>
                      <h4>Phng nga</h4>
                      <ul>
                        <li>Thc hnh tnh dc an ton</li>
                        <li>Tim phng cc bnh truyn nhim theo khuyn co</li>
                        <li>Trnh tip xc vi ngi mc bnh truyn nhim</li>
                        <li>Gi v sinh c nhn tt</li>
                        <li>Thng bo cho i tc v tnh trng HIV ca bn</li>
                      </ul>
                    </div>
                    
                    <div className="instruction-card">
                      <div className="instruction-icon mental-icon">
                        <i className="fas fa-brain"></i>
                      </div>
                      <h4>Sc khe tm thn</h4>
                      <ul>
                        <li>Nhn bit du hiu trm cm v lo u</li>
                        <li>Tm kim s h tr tm l khi cn</li>
                        <li>Duy tr kt ni x hi v mi quan h</li>
                        <li>Thc hnh cc k thut th gin nh thin</li>
                        <li>t mc tiu thc t v tch cc</li>
                      </ul>
                    </div>
                  </div>
                  
                  <div className="special-instructions">
                    <h4><i className="fas fa-exclamation-circle"></i> Hng dn c bit</h4>
                    <div className="special-instructions-content">
                      <p>{treatmentPlan.specialInstructions || 'Khng c hng dn c bit no t bc s.'}</p>
                    </div>
                  </div>
                </div>
              )}
              
              {/* Appointments Tab */}
              {activeTab === 'appointments' && (
                <div className="appointments-tab">
                  <div className="appointments-header">
                    <h3><i className="fas fa-calendar-check"></i> Lch hn khm</h3>
                    <Link to="/appointment" className="new-appointment-btn">
                      <i className="fas fa-plus"></i> t lch hn mi
                    </Link>
                  </div>
                  
                  {treatmentPlan.appointments && treatmentPlan.appointments.length > 0 ? (
                    <>
                      <div className="next-appointment">
                        <h4>Lch hn sp ti</h4>
                        {treatmentPlan.appointments.filter(app => new Date(app.date) > new Date()).length > 0 ? (
                          <div className="next-appointment-card">
                            {(() => {
                              const nextApp = treatmentPlan.appointments
                                .filter(app => new Date(app.date) > new Date())
                                .sort((a, b) => new Date(a.date) - new Date(b.date))[0];
                              
                              return (
                                <>
                                  <div className="appointment-date">
                                    <div className="date-card">
                                      <div className="date-month">{new Date(nextApp.date).toLocaleString('vi', { month: 'short' })}</div>
                                      <div className="date-day">{new Date(nextApp.date).getDate()}</div>
                                      <div className="date-year">{new Date(nextApp.date).getFullYear()}</div>
                                    </div>
                                    <div className="date-time">{nextApp.time}</div>
                                  </div>
                                  
                                  <div className="appointment-info">
                                    <h5>{nextApp.type}</h5>
                                    <div className="info-item">
                                      <i className="fas fa-user-md"></i>
                                      <span>{nextApp.doctor.name}</span>
                                    </div>
                                    <div className="info-item">
                                      <i className="fas fa-clinic-medical"></i>
                                      <span>{nextApp.location}</span>
                                    </div>
                                    <div className="info-item">
                                      <i className="fas fa-info-circle"></i>
                                      <span>{nextApp.notes || 'Khng c ghi ch'}</span>
                                    </div>
                                  </div>
                                  
                                  <div className="appointment-actions">
                                    <button className="action-btn reschedule-btn">
                                      <i className="fas fa-sync-alt"></i> i lch
                                    </button>
                                    <button className="action-btn cancel-btn">
                                      <i className="fas fa-times"></i> Hy
                                    </button>
                                  </div>
                                </>
                              );
                            })()}
                          </div>
                        ) : (
                          <div className="no-data-message">
                            <p>Khng c lch hn sp ti. <Link to="/appointment">t lch hn mi</Link></p>
                          </div>
                        )}
                      </div>
                      
                      <div className="appointments-history">
                        <h4>Lch s khm</h4>
                        <div className="table-container">
                          <table>
                            <thead>
                              <tr>
                                <th>Ngy</th>
                                <th>Thi gian</th>
                                <th>Loi</th>
                                <th>Bc s</th>
                                <th>a im</th>
                                <th>Kt qu</th>
                              </tr>
                            </thead>
                            <tbody>
                              {treatmentPlan.appointments
                                .filter(app => new Date(app.date) < new Date())
                                .sort((a, b) => new Date(b.date) - new Date(a.date))
                                .map((appointment, index) => (
                                  <tr key={index}>
                                    <td>{formatDate(appointment.date)}</td>
                                    <td>{appointment.time}</td>
                                    <td>{appointment.type}</td>
                                    <td>{appointment.doctor.name}</td>
                                    <td>{appointment.location}</td>
                                    <td>
                                      {appointment.status === 'completed' ? (
                                        <span className="status-badge completed"> khm</span>
                                      ) : appointment.status === 'cancelled' ? (
                                        <span className="status-badge cancelled"> hy</span>
                                      ) : (
                                        <span className="status-badge pending">Cha khm</span>
                                      )}
                                    </td>
                                  </tr>
                                ))}
                            </tbody>
                          </table>
                        </div>
                      </div>
                    </>
                  ) : (
                    <div className="no-data-message">
                      <p>Khng c thng tin v lch hn khm. <Link to="/appointment">t lch hn</Link>  c t vn.</p>
                    </div>
                  )}
                </div>
              )}
            </div>
          </div>
        </div>
      </section>

      {/* Bottom Action Bar */}
      <div className="fixed-action-bar">
        <div className="container">
          <div className="action-bar-content">
            <div className="action-info">
              <i className="fas fa-info-circle"></i>
              <span>Cn tr gip? Lin h ng dy nng: <strong>1800 1234</strong></span>
            </div>
            <div className="action-buttons">
              <Link to="/profile" className="action-btn">
                <i className="fas fa-user"></i> H s
              </Link>
              <Link to="/resources" className="action-btn">
                <i className="fas fa-book-medical"></i> Ti nguyn
              </Link>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default TreatmentPlan;