import React, { createContext, useState, useEffect, useContext } from 'react';

// To Context
export const AuthContext = createContext(null);

// Custom hook  s dng AuthContext
export const useAuth = () => useContext(AuthContext);

export const AuthProvider = ({ children }) => {
  const [currentUser, setCurrentUser] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    // Kim tra nu ngi dng  ng nhp trc  (t localStorage)
    const storedUser = localStorage.getItem('hivAppUser');
    if (storedUser) {
      try {
        setCurrentUser(JSON.parse(storedUser));
      } catch (err) {
        console.error("Error parsing stored user:", err);
        localStorage.removeItem('hivAppUser');
      }
    }
    setLoading(false);
  }, []);

  // Hm ng nhp - trong thc t s gi API
  const login = async (email, password) => {
    try {
      setError(null);
      // M phng gi API
      // Trong d n thc t, thay bng gi API n server
      if (email && password) {
        // Gi lp user
        const user = {
          id: "user123",
          name: "Nguyn Vn A",
          email: email,
          role: "patient"
        };
        
        // Lu thng tin user vo localStorage
        localStorage.setItem('hivAppUser', JSON.stringify(user));
        setCurrentUser(user);
        return true;
      } else {
        throw new Error("Thng tin ng nhp khng ng");
      }
    } catch (err) {
      setError(err.message);
      return false;
    }
  };

  // Hm ng k - trong thc t s gi API
  const register = async (name, email, password) => {
    try {
      setError(null);
      // M phng gi API
      if (name && email && password) {
        // Gi lp user mi
        const newUser = {
          id: "user" + Math.floor(Math.random() * 1000),
          name: name,
          email: email,
          role: "patient"
        };
        
        // Lu thng tin user vo localStorage
        localStorage.setItem('hivAppUser', JSON.stringify(newUser));
        setCurrentUser(newUser);
        return true;
      } else {
        throw new Error("Vui lng in y  thng tin");
      }
    } catch (err) {
      setError(err.message);
      return false;
    }
  };

  // Hm ng xut
  const logout = () => {
    localStorage.removeItem('hivAppUser');
    setCurrentUser(null);
  };

  // Hm qun mt khu
  const resetPassword = async (email) => {
    try {
      setError(null);
      // M phng gi API
      if (email) {
        // M phng gi email t li mt khu
        console.log(` gi yu cu t li mt khu n ${email}`);
        return true;
      } else {
        throw new Error("Vui lng nhp a ch email");
      }
    } catch (err) {
      setError(err.message);
      return false;
    }
  };

  // Hm cp nht thng tin profile
  const updateProfile = async (userData) => {
    try {
      setError(null);
      // M phng cp nht thng tin
      if (currentUser && userData) {
        const updatedUser = { ...currentUser, ...userData };
        localStorage.setItem('hivAppUser', JSON.stringify(updatedUser));
        setCurrentUser(updatedUser);
        return true;
      } else {
        throw new Error("Khng th cp nht thng tin");
      }
    } catch (err) {
      setError(err.message);
      return false;
    }
  };

  const value = {
    currentUser,
    loading,
    error,
    login,
    register,
    logout,
    resetPassword,
    updateProfile
  };

  return <AuthContext.Provider value={value}>{children}</AuthContext.Provider>;
};

export default AuthContext;