import React from 'react';
import { Link } from 'react-router-dom';
import Navbar from '../components/Navbar';
import '../styles/Department.css';

const HIVDepartment = () => {
  // M phng d liu i ng y t
  const medicalTeam = [
    {
      id: 1,
      name: 'GS.TS. Nguyn Vn A',
      role: 'Trng khoa HIV/AIDS',
      image: '/doctor-1.jpg',
      specialization: 'Chuyn gia v bnh truyn nhim & HIV',
      experience: '30 nm kinh nghim',
      education: 'Tin s Y khoa, i hc Y H Ni',
      bio: 'GS.TS. Nguyn Vn A l mt trong nhng chuyn gia hng u v HIV/AIDS ti Vit Nam. ng  c nhiu ng gp quan trng trong lnh vc nghin cu v iu tr HIV.'
    },
    {
      id: 2,
      name: 'PGS.TS. Trn Th B',
      role: 'Ph khoa HIV/AIDS',
      image: '/doctor-2.jpg',
      specialization: 'Chuyn gia v iu tr ARV & nhim trng c hi',
      experience: '25 nm kinh nghim',
      education: 'Tin s Y khoa, i hc Y Dc TP.HCM',
      bio: 'PGS.TS. Trn Th B c nhiu nm nghin cu v iu tr cc bnh nhim trng c hi  bnh nhn HIV/AIDS. B  tham gia nhiu d n quc t v phng chng HIV.'
    },
    {
      id: 3,
      name: 'TS.BS. L Vn C',
      role: 'Trng n v iu tr',
      image: '/doctor-3.jpg',
      specialization: 'Chuyn gia v qun l iu tr HIV & ng nhim HIV-Lao',
      experience: '20 nm kinh nghim',
      education: 'Tin s Y khoa, i hc Y H Ni',
      bio: 'TS.BS. L Vn C l chuyn gia v qun l iu tr HIV v ng nhim HIV-Lao. ng  c nhiu cng trnh nghin cu c ng trn cc tp ch quc t uy tn.'
    },
    {
      id: 4,
      name: 'ThS.BS. Phm Th D',
      role: 'Trng n v t vn',
      image: '/doctor-4.jpg',
      specialization: 'Chuyn gia t vn tm l & tun th iu tr',
      experience: '15 nm kinh nghim',
      education: 'Thc s Tm l hc lm sng, i hc Khoa hc X hi v Nhn vn',
      bio: 'ThS.BS. Phm Th D chuyn v t vn tm l v h tr bnh nhn tun th iu tr. B  pht trin nhiu chng trnh t vn hiu qu cho ngi nhim HIV.'
    }
  ];

  // M phng d liu dch v ca khoa
  const services = [
    {
      id: 1,
      title: 'Xt nghim v chn on HIV',
      icon: 'fa-vial',
      description: 'Xt nghim nhanh v chnh xc vi cng ngh tin tin, m bo kt qu chnh xc v bo mt.',
      features: [
        'Xt nghim sng lc HIV',
        'Xt nghim khng nh HIV',
        'Xt nghim ti lng virus',
        'Xt nghim CD4'
      ]
    },
    {
      id: 2,
      title: 'iu tr ARV',
      icon: 'fa-pills',
      description: 'Phc  iu tr ARV hin i theo hng dn quc gia v quc t, gip kim sot virus HIV hiu qu.',
      features: [
        'Phc  iu tr c nhn ha',
        'Thuc ARV th h mi',
        'Theo di iu tr nh k',
        'Qun l tc dng ph'
      ]
    },
    {
      id: 3,
      title: 'Qun l v iu tr bnh ng nhim',
      icon: 'fa-virus',
      description: 'iu tr ton din cc bnh ng nhim thng gp  ngi nhim HIV nh lao, vim gan virus.',
      features: [
        'iu tr ng nhim HIV-Lao',
        'iu tr ng nhim HIV-Vim gan',
        'Qun l nhim trng c hi',
        'Phng nga bnh ng nhim'
      ]
    },
    {
      id: 4,
      title: 'T vn v h tr tm l',
      icon: 'fa-hands-helping',
      description: 'Dch v t vn chuyn nghip, gip ngi bnh v gia nh i ph vi thch thc tm l khi sng vi HIV.',
      features: [
        'T vn trc v sau xt nghim',
        'T vn tun th iu tr',
        'H tr tm l c nhn v nhm',
        'Kt ni vi nhm h tr ng ng'
      ]
    },
    {
      id: 5,
      title: 'Phng nga ly truyn HIV',
      icon: 'fa-shield-alt',
      description: 'Cc bin php can thip hiu qu  ngn nga ly truyn HIV t m sang con v gia cc c nhn.',
      features: [
        'D phng trc phi nhim (PrEP)',
        'D phng sau phi nhim (PEP)',
        'Phng ly truyn t m sang con',
        'T vn gim hi'
      ]
    },
    {
      id: 6,
      title: 'Chm sc dinh dng',
      icon: 'fa-apple-alt',
      description: 'nh gi v t vn dinh dng c nhn ha  nng cao sc khe v hiu qu iu tr cho ngi nhim HIV.',
      features: [
        'nh gi tnh trng dinh dng',
        'Lp k hoch dinh dng c nhn',
        'T vn b sung vi cht',
        'Qun l tc dng ph qua dinh dng'
      ]
    }
  ];

  // M phng d liu thnh tu v con s
  const statistics = [
    { id: 1, number: '15,000+', title: 'Bnh nhn c iu tr', icon: 'fa-user-md' },
    { id: 2, number: '95%', title: 'T l iu tr thnh cng', icon: 'fa-chart-line' },
    { id: 3, number: '25+', title: 'Nm kinh nghim', icon: 'fa-history' },
    { id: 4, number: '30+', title: 'Chuyn gia y t', icon: 'fa-users' }
  ];

  // M phng d liu cng trnh nghin cu
  const researchProjects = [
    {
      id: 1,
      title: 'Nghin cu hiu qu ca phc  iu tr ARV mi trn bnh nhn HIV ti Vit Nam',
      authors: 'GS.TS. Nguyn Vn A, PGS.TS. Trn Th B',
      year: '2022',
      journal: 'Tp ch Y hc Vit Nam',
      summary: 'Nghin cu nh gi hiu qu ca phc  iu tr ARV th h mi trn 500 bnh nhn HIV ti Vit Nam.'
    },
    {
      id: 2,
      title: 'nh gi tc ng ca chng trnh t vn tun th iu tr cho bnh nhn HIV',
      authors: 'ThS.BS. Phm Th D, TS.BS. L Vn C',
      year: '2023',
      journal: 'Tp ch Y t Cng cng',
      summary: 'Nghin cu cho thy chng trnh t vn tun th iu tr lm tng t l tun th t 70% ln 92%.'
    },
    {
      id: 3,
      title: 'Phn tch cc yu t nh hng n cht lng cuc sng ca ngi sng vi HIV ti Vit Nam',
      authors: 'PGS.TS. Trn Th B v cng s',
      year: '2024',
      journal: 'International Journal of HIV Research',
      summary: 'Nghin cu phn tch cc yu t x hi, kinh t v y t nh hng n cht lng cuc sng ca 1,200 ngi sng vi HIV.'
    }
  ];

  return (
    <>
      <Navbar />
      <div className="department-container">
        {/* Banner Section */}
        <div className="department-banner">
          <div className="banner-overlay"></div>
          <div className="banner-content">
            <h1>Khoa HIV/AIDS</h1>
            <p>Chm sc ton din v cht lng cao cho ngi sng vi HIV</p>
          </div>
        </div>
        
        {/* Introduction Section */}
        <section className="department-intro">
          <div className="section-container">
            <div className="section-header">
              <h2>Gii thiu khoa HIV/AIDS</h2>
              <div className="section-divider"></div>
            </div>
            
            <div className="intro-content">
              <div className="intro-text">
                <p className="intro-highlight">Khoa HIV/AIDS c thnh lp t nm 2000, l mt trong nhng n v chuyn khoa hng u v iu tr v chm sc HIV/AIDS ti Vit Nam.</p>
                
                <p>Vi i ng y bc s giu kinh nghim v trang thit b hin i, chng ti cung cp dch v chm sc ton din cho ngi sng vi HIV, bao gm chn on, iu tr, t vn tm l v h tr x hi.</p>
                
                <p>Khoa HIV/AIDS l n v tin phong trong vic ng dng cc phc  iu tr tin tin v tham gia nhiu nghin cu quc t v HIV/AIDS. Chng ti cam kt mang li dch v chm sc cht lng cao trong mt mi trng tn trng, ring t v khng phn bit i x.</p>
              </div>
              
              <div className="intro-image">
                <img src="/department-image.jpg" alt="Khoa HIV/AIDS" />
              </div>
            </div>
          </div>
        </section>
        
        {/* Services Section */}
        <section className="department-services">
          <div className="section-container">
            <div className="section-header">
              <h2>Dch v ca chng ti</h2>
              <div className="section-divider"></div>
              <p className="section-subtitle">Khoa HIV/AIDS cung cp cc dch v chm sc ton din cho ngi sng vi HIV</p>
            </div>
            
            <div className="services-grid">
              {services.map(service => (
                <div className="service-card" key={service.id}>
                  <div className="service-icon">
                    <i className={`fas ${service.icon}`}></i>
                  </div>
                  <h3>{service.title}</h3>
                  <p>{service.description}</p>
                  <ul className="service-features">
                    {service.features.map((feature, index) => (
                      <li key={index}>
                        <i className="fas fa-check-circle"></i>
                        <span>{feature}</span>
                      </li>
                    ))}
                  </ul>
                </div>
              ))}
            </div>
          </div>
        </section>
        
        {/* Statistics Section */}
        <section className="department-statistics">
          <div className="statistics-overlay"></div>
          <div className="section-container">
            <div className="statistics-grid">
              {statistics.map(stat => (
                <div className="statistic-item" key={stat.id}>
                  <div className="statistic-icon">
                    <i className={`fas ${stat.icon}`}></i>
                  </div>
                  <div className="statistic-number">{stat.number}</div>
                  <div className="statistic-title">{stat.title}</div>
                </div>
              ))}
            </div>
          </div>
        </section>
        
        {/* Medical Team Section */}
        <section className="department-team">
          <div className="section-container">
            <div className="section-header">
              <h2>i ng y t</h2>
              <div className="section-divider"></div>
              <p className="section-subtitle">Gp g cc chuyn gia u ngnh ca chng ti trong lnh vc HIV/AIDS</p>
            </div>
            
            <div className="team-grid">
              {medicalTeam.map(doctor => (
                <div className="doctor-card" key={doctor.id}>
                  <div className="doctor-image">
                    <img src={doctor.image} alt={doctor.name} />
                  </div>
                  <div className="doctor-info">
                    <h3>{doctor.name}</h3>
                    <div className="doctor-role">{doctor.role}</div>
                    <div className="doctor-specialization">{doctor.specialization}</div>
                    <p className="doctor-bio">{doctor.bio}</p>
                    <div className="doctor-details">
                      <div className="detail-item">
                        <i className="fas fa-graduation-cap"></i>
                        <span>{doctor.education}</span>
                      </div>
                      <div className="detail-item">
                        <i className="fas fa-briefcase"></i>
                        <span>{doctor.experience}</span>
                      </div>
                    </div>
                    <button className="appointment-btn">
                      <i className="fas fa-calendar-check"></i>
                      t lch hn
                    </button>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </section>
        
        {/* Research Section */}
        <section className="department-research">
          <div className="section-container">
            <div className="section-header">
              <h2>Nghin cu khoa hc</h2>
              <div className="section-divider"></div>
              <p className="section-subtitle">Cc cng trnh nghin cu ni bt t i ng chuyn gia ca chng ti</p>
            </div>
            
            <div className="research-list">
              {researchProjects.map(project => (
                <div className="research-item" key={project.id}>
                  <div className="research-content">
                    <h3>{project.title}</h3>
                    <div className="research-meta">
                      <div className="meta-item">
                        <i className="fas fa-users"></i>
                        <span>{project.authors}</span>
                      </div>
                      <div className="meta-item">
                        <i className="fas fa-calendar-alt"></i>
                        <span>{project.year}</span>
                      </div>
                      <div className="meta-item">
                        <i className="fas fa-book-open"></i>
                        <span>{project.journal}</span>
                      </div>
                    </div>
                    <p>{project.summary}</p>
                  </div>
                  <div className="research-actions">
                    <button className="research-btn">
                      <i className="fas fa-file-pdf"></i>
                      Xem bi nghin cu
                    </button>
                  </div>
                </div>
              ))}
            </div>
            
            <div className="research-more">
              <button className="view-all-btn">
                <span>Xem tt c nghin cu</span>
                <i className="fas fa-arrow-right"></i>
              </button>
            </div>
          </div>
        </section>
        
        {/* Appointment Section */}
        <section className="department-appointment">
          <div className="section-container">
            <div className="appointment-box">
              <div className="appointment-content">
                <h2>t lch khm ti Khoa HIV/AIDS</h2>
                <p>Hy t lch hn trc tuyn  c khm v t vn bi cc chuyn gia u ngnh ca chng ti.</p>
                <div className="appointment-features">
                  <div className="feature-item">
                    <i className="fas fa-check-circle"></i>
                    <span>i ng y bc s chuyn khoa giu kinh nghim</span>
                  </div>
                  <div className="feature-item">
                    <i className="fas fa-check-circle"></i>
                    <span>Trang thit b hin i, phng php iu tr tin tin</span>
                  </div>
                  <div className="feature-item">
                    <i className="fas fa-check-circle"></i>
                    <span>Mi trng ring t, tn trng v bo mt thng tin</span>
                  </div>
                </div>
                <Link to="/appointments" className="make-appointment-btn">
                  <i className="fas fa-calendar-plus"></i>
                  t lch ngay
                </Link>
              </div>
              <div className="appointment-image">
                <img src="/appointment-image.jpg" alt="t lch khm" />
              </div>
            </div>
          </div>
        </section>
        
        {/* FAQ Section */}
        <section className="department-faq">
          <div className="section-container">
            <div className="section-header">
              <h2>Cu hi thng gp</h2>
              <div className="section-divider"></div>
              <p className="section-subtitle">Gii p nhng thc mc ph bin v HIV/AIDS v dch v ca chng ti</p>
            </div>
            
            <div className="faq-container">
              <div className="faq-item">
                <div className="faq-question">
                  <h3>Khi no ti nn xt nghim HIV?</h3>
                  <i className="fas fa-chevron-down"></i>
                </div>
                <div className="faq-answer">
                  <p>Bn nn xt nghim HIV nu bn c nguy c phi nhim nh quan h tnh dc khng bo v, dng chung kim tim, hoc tip xc vi mu ca ngi nhim HIV. Vic xt nghim nh k c khuyn ngh cho nhng ngi c nhiu bn tnh hoc thuc nhm nguy c cao.</p>
                </div>
              </div>
              
              <div className="faq-item">
                <div className="faq-question">
                  <h3>Thuc ARV c tc dng ph g khng?</h3>
                  <i className="fas fa-chevron-down"></i>
                </div>
                <div className="faq-answer">
                  <p>Thuc ARV hin i c t tc dng ph hn so vi trc y. Cc tc dng ph thng gp c th bao gm bun nn, au u, mt mi trong vi tun u iu tr. Hu ht tc dng ph s gim dn theo thi gian. Tuy nhin, mi ngi c th c phn ng khc nhau vi thuc, v vy cn thng bo cho bc s nu gp bt k vn  no.</p>
                </div>
              </div>
              
              <div className="faq-item">
                <div className="faq-question">
                  <h3>Ti c th sng bao lu nu nhim HIV?</h3>
                  <i className="fas fa-chevron-down"></i>
                </div>
                <div className="faq-answer">
                  <p>Vi cc phc  iu tr ARV hin i, ngi nhim HIV c th sng khe mnh v c tui th gn nh bnh thng. iu quan trng l pht hin sm, iu tr ARV ng cch v tun th phc  iu tr. Nhiu ngi nhim HIV hin nay c th sng hng chc nm m khng pht trin thnh AIDS.</p>
                </div>
              </div>
              
              <div className="faq-item">
                <div className="faq-question">
                  <h3>Lm th no  ng k khm ti Khoa HIV/AIDS?</h3>
                  <i className="fas fa-chevron-down"></i>
                </div>
                <div className="faq-answer">
                  <p>Bn c th ng k khm ti Khoa HIV/AIDS bng cch t lch trc tuyn thng qua website hoc ng dng ca bnh vin, gi in n s hotline 1900-6889, hoc n trc tip quy ng k ca khoa. Chng ti khuyn khch t lch trc  gim thi gian ch i v chun b tt nht cho bui khm.</p>
                </div>
              </div>
            </div>
            
            <div className="faq-more">
              <Link to="/resources" className="view-more-btn">
                <span>Xem thm cu hi thng gp</span>
                <i className="fas fa-arrow-right"></i>
              </Link>
            </div>
          </div>
        </section>
        
        {/* Contact Section */}
        <section className="department-contact">
          <div className="section-container">
            <div className="contact-box">
              <div className="contact-info">
                <h2>Lin h vi chng ti</h2>
                <p>Nu bn c bt k cu hi no hoc cn c t vn, vui lng lin h vi chng ti qua:</p>
                
                <div className="contact-details">
                  <div className="contact-item">
                    <i className="fas fa-phone-alt"></i>
                    <div>
                      <h3>in thoi</h3>
                      <p>Hotline: 1900-6889</p>
                      <p>Vn phng khoa: 024-1234-5678</p>
                    </div>
                  </div>
                  
                  <div className="contact-item">
                    <i className="fas fa-envelope"></i>
                    <div>
                      <h3>Email</h3>
                      <p>hiv.department@hivhospital.vn</p>
                      <p>info@hivhospital.vn</p>
                    </div>
                  </div>
                  
                  <div className="contact-item">
                    <i className="fas fa-map-marker-alt"></i>
                    <div>
                      <h3>a ch</h3>
                      <p>Khoa HIV/AIDS - Tng 3, Nh A1</p>
                      <p>Bnh vin Bch Mai, H Ni, Vit Nam</p>
                    </div>
                  </div>
                  
                  <div className="contact-item">
                    <i className="fas fa-clock"></i>
                    <div>
                      <h3>Gi lm vic</h3>
                      <p>Th Hai - Th Su: 7:30 - 17:00</p>
                      <p>Th By: 7:30 - 12:00</p>
                    </div>
                  </div>
                </div>
              </div>
              
              <div className="contact-form">
                <h3>Gi tin nhn cho chng ti</h3>
                <form>
                  <div className="form-group">
                    <input type="text" placeholder="H v tn" required />
                  </div>
                  
                  <div className="form-group">
                    <input type="email" placeholder="Email" required />
                  </div>
                  
                  <div className="form-group">
                    <input type="tel" placeholder="S in thoi" />
                  </div>
                  
                  <div className="form-group">
                    <select>
                      <option value="">Chn ch </option>
                      <option value="appointment">t lch khm</option>
                      <option value="consultation">T vn iu tr</option>
                      <option value="information">Thng tin chung</option>
                      <option value="other">Khc</option>
                    </select>
                  </div>
                  
                  <div className="form-group">
                    <textarea placeholder="Ni dung tin nhn" rows="5" required></textarea>
                  </div>
                  
                  <button type="submit" className="send-message-btn">
                    <i className="fas fa-paper-plane"></i>
                    Gi tin nhn
                  </button>
                </form>
              </div>
            </div>
          </div>
        </section>
      </div>
    </>
  );
};

export default HIVDepartment;