import React from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import { AuthProvider } from './context/AuthContext';
import './App.css';

// Import cc trang
import HospitalHome from './pages/HospitalHome';
import Home from './pages/Home';
import Login from './pages/Login';
import Register from './pages/Register';
import ForgotPassword from './pages/ForgotPassword';
import Profile from './pages/Profile';
import HIVDepartment from './pages/HIVDepartment';
import Resources from './pages/Resources';
import TreatmentPlan from './pages/TreatmentPlan';
import MedicationManager from './pages/MedicationManager';
import AppointmentManager from './pages/AppointmentManager';

function App() {
  return (
    <AuthProvider>
      <Router>
        <Routes>
          {/* Trang ch chnh ca ng dng */}
          <Route path="/" element={<Home />} />
          
          {/* Trang ch bnh vin */}
          <Route path="/hospital" element={<HospitalHome />} />
          
          {/* Cc trang xc thc */}
          <Route path="/login" element={<Login />} />
          <Route path="/register" element={<Register />} />
          <Route path="/forgot-password" element={<ForgotPassword />} />
          
          {/* Cc trang thuc bnh vin */}
          <Route path="/hospital/gioi-thieu" element={<div>Trang Gii thiu</div>} />
          <Route path="/hospital/chuyen-khoa" element={<div>Trang Chuyn khoa</div>} />
          <Route path="/hospital/lich-kham" element={<div>Trang Lch khm</div>} />
          <Route path="/hospital/tin-tuc" element={<div>Trang Tin tc</div>} />
          <Route path="/hospital/lien-he" element={<div>Trang Lin h</div>} />
          
          {/* Cc trang chc nng ca ng dng */}
          <Route path="/profile" element={<Profile />} />
          <Route path="/hiv-department" element={<HIVDepartment />} />
          <Route path="/resources" element={<Resources />} />
          <Route path="/treatment-plan" element={<TreatmentPlan />} />
          <Route path="/medication" element={<MedicationManager />} />
          <Route path="/appointments" element={<AppointmentManager />} />
          
          {/* Chuyn hng cho cc URL khng hp l */}
          <Route path="*" element={<Navigate to="/" replace />} />
        </Routes>
      </Router>
    </AuthProvider>
  );
}

export default App;