import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';
import Navbar from '../components/Navbar';
import '../styles/Register.css';

const Register = () => {
  const [formData, setFormData] = useState({
    fullName: '',
    email: '',
    password: '',
    confirmPassword: '',
    phoneNumber: '',
    dateOfBirth: '',
    gender: '',
    agreeTerms: false
  });
  
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [formError, setFormError] = useState('');
  const [step, setStep] = useState(1);
  
  const { register, error } = useAuth();
  const navigate = useNavigate();

  const handleChange = (e) => {
    const { name, value, type, checked } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: type === 'checkbox' ? checked : value
    }));
  };

  const validateStep1 = () => {
    if (!formData.fullName || !formData.email || !formData.password || !formData.confirmPassword) {
      setFormError('Vui lng in y  thng tin');
      return false;
    }
    
    if (formData.password.length < 8) {
      setFormError('Mt khu phi c t nht 8 k t');
      return false;
    }
    
    if (formData.password !== formData.confirmPassword) {
      setFormError('Mt khu xc nhn khng khp');
      return false;
    }
    
    const emailPattern = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    if (!emailPattern.test(formData.email)) {
      setFormError('Email khng hp l');
      return false;
    }
    
    return true;
  };

  const validateStep2 = () => {
    if (!formData.phoneNumber || !formData.dateOfBirth || !formData.gender) {
      setFormError('Vui lng in y  thng tin');
      return false;
    }
    
    if (!formData.agreeTerms) {
      setFormError('Bn cn ng  vi iu khon dch v');
      return false;
    }
    
    return true;
  };

  const handleNext = () => {
    if (validateStep1()) {
      setFormError('');
      setStep(2);
    }
  };

  const handleBack = () => {
    setStep(1);
    setFormError('');
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (!validateStep2()) {
      return;
    }
    
    setIsSubmitting(true);
    setFormError('');
    
    try {
      const success = await register(
        formData.fullName,
        formData.email, 
        formData.password
      );
      
      if (success) {
        navigate('/profile', { state: { newUser: true } });
      } else {
        setFormError(error || 'ng k tht bi');
      }
    } catch (err) {
      setFormError(' xy ra li khi ng k');
      console.error(err);
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <>
      <Navbar />
      <div className="register-container">
        <div className="register-card">
          <div className="register-header">
            <h2>ng k ti khon</h2>
            <p>To ti khon  qun l vic iu tr HIV ca bn</p>
            
            <div className="register-steps">
              <div className={`step ${step >= 1 ? 'active' : ''}`}>
                <div className="step-number">1</div>
                <div className="step-text">Thng tin ti khon</div>
              </div>
              <div className="step-connector"></div>
              <div className={`step ${step >= 2 ? 'active' : ''}`}>
                <div className="step-number">2</div>
                <div className="step-text">Thng tin c nhn</div>
              </div>
            </div>
          </div>
          
          {formError && <div className="register-error">{formError}</div>}
          
          <form onSubmit={handleSubmit} className="register-form">
            {step === 1 && (
              <div className="form-step">
                <div className="form-group">
                  <label htmlFor="fullName">H v tn</label>
                  <div className="input-with-icon">
                    <i className="fas fa-user"></i>
                    <input
                      type="text"
                      id="fullName"
                      name="fullName"
                      value={formData.fullName}
                      onChange={handleChange}
                      placeholder="Nhp h v tn y "
                      required
                    />
                  </div>
                </div>
                
                <div className="form-group">
                  <label htmlFor="email">Email</label>
                  <div className="input-with-icon">
                    <i className="fas fa-envelope"></i>
                    <input
                      type="email"
                      id="email"
                      name="email"
                      value={formData.email}
                      onChange={handleChange}
                      placeholder="Nhp a ch email"
                      required
                    />
                  </div>
                </div>
                
                <div className="form-group">
                  <label htmlFor="password">Mt khu</label>
                  <div className="input-with-icon">
                    <i className="fas fa-lock"></i>
                    <input
                      type="password"
                      id="password"
                      name="password"
                      value={formData.password}
                      onChange={handleChange}
                      placeholder="To mt khu (t nht 8 k t)"
                      required
                    />
                  </div>
                </div>
                
                <div className="form-group">
                  <label htmlFor="confirmPassword">Xc nhn mt khu</label>
                  <div className="input-with-icon">
                    <i className="fas fa-lock"></i>
                    <input
                      type="password"
                      id="confirmPassword"
                      name="confirmPassword"
                      value={formData.confirmPassword}
                      onChange={handleChange}
                      placeholder="Nhp li mt khu"
                      required
                    />
                  </div>
                </div>
                
                <button 
                  type="button" 
                  className="register-button"
                  onClick={handleNext}
                >
                  Tip theo
                </button>
              </div>
            )}
            
            {step === 2 && (
              <div className="form-step">
                <div className="form-group">
                  <label htmlFor="phoneNumber">S in thoi</label>
                  <div className="input-with-icon">
                    <i className="fas fa-phone"></i>
                    <input
                      type="tel"
                      id="phoneNumber"
                      name="phoneNumber"
                      value={formData.phoneNumber}
                      onChange={handleChange}
                      placeholder="Nhp s in thoi"
                      required
                    />
                  </div>
                </div>
                
                <div className="form-group">
                  <label htmlFor="dateOfBirth">Ngy sinh</label>
                  <div className="input-with-icon">
                    <i className="fas fa-calendar"></i>
                    <input
                      type="date"
                      id="dateOfBirth"
                      name="dateOfBirth"
                      value={formData.dateOfBirth}
                      onChange={handleChange}
                      required
                    />
                  </div>
                </div>
                
                <div className="form-group">
                  <label>Gii tnh</label>
                  <div className="gender-options">
                    <div className="gender-option">
                      <input 
                        type="radio" 
                        id="male" 
                        name="gender" 
                        value="male"
                        checked={formData.gender === 'male'}
                        onChange={handleChange}
                        required
                      />
                      <label htmlFor="male">Nam</label>
                    </div>
                    
                    <div className="gender-option">
                      <input 
                        type="radio" 
                        id="female" 
                        name="gender" 
                        value="female"
                        checked={formData.gender === 'female'}
                        onChange={handleChange}
                      />
                      <label htmlFor="female">N</label>
                    </div>
                    
                    <div className="gender-option">
                      <input 
                        type="radio" 
                        id="other" 
                        name="gender" 
                        value="other"
                        checked={formData.gender === 'other'}
                        onChange={handleChange}
                      />
                      <label htmlFor="other">Khc</label>
                    </div>
                  </div>
                </div>
                
                <div className="form-group terms-group">
                  <div className="checkbox-container">
                    <input
                      type="checkbox"
                      id="agreeTerms"
                      name="agreeTerms"
                      checked={formData.agreeTerms}
                      onChange={handleChange}
                      required
                    />
                    <label htmlFor="agreeTerms">
                      Ti ng  vi <a href="#">iu khon dch v</a> v <a href="#">Chnh sch bo mt</a>
                    </label>
                  </div>
                </div>
                
                <div className="form-actions">
                  <button 
                    type="button" 
                    className="back-button"
                    onClick={handleBack}
                  >
                    Quay li
                  </button>
                  
                  <button 
                    type="submit" 
                    className="register-button"
                    disabled={isSubmitting}
                  >
                    {isSubmitting ? 'ang x l...' : 'Hon tt ng k'}
                  </button>
                </div>
              </div>
            )}
          </form>
          
          <div className="register-footer">
            <p> c ti khon? <Link to="/login">ng nhp</Link></p>
          </div>
        </div>
      </div>
    </>
  );
};

export default Register;