import React, { useState, useEffect } from 'react';
import { useNavigate, useLocation, Link } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';
import Navbar from '../components/Navbar';
import '../styles/Profile.css';

const Profile = () => {
  const { currentUser, updateProfile, logout } = useAuth();
  const navigate = useNavigate();
  const location = useLocation();
  const isNewUser = location.state?.newUser || false;

  const [activeTab, setActiveTab] = useState('personal');
  const [editMode, setEditMode] = useState(false);
  const [notification, setNotification] = useState(isNewUser ? {
    type: 'success',
    message: 'ng k thnh cng! Vui lng cp nht thng tin c nhn ca bn.'
  } : null);
  
  const [formData, setFormData] = useState({
    fullName: currentUser?.name || '',
    email: currentUser?.email || '',
    phoneNumber: currentUser?.phoneNumber || '',
    dateOfBirth: currentUser?.dateOfBirth || '',
    gender: currentUser?.gender || '',
    address: currentUser?.address || '',
    bloodType: currentUser?.bloodType || '',
    emergencyContact: currentUser?.emergencyContact || '',
    allergies: currentUser?.allergies || '',
    currentMedications: currentUser?.currentMedications || '',
  });

  // M phng d liu lch s khm bnh
  const [medicalHistory] = useState([
    {
      id: 1,
      date: '15/04/2025',
      doctor: 'BS. Nguyn Vn A',
      diagnosis: 'Ti khm nh k',
      notes: 'Tnh trng n nh, tip tc phc  iu tr hin ti',
      nextAppointment: '15/05/2025'
    },
    {
      id: 2,
      date: '15/03/2025',
      doctor: 'BS. Trn Th B',
      diagnosis: 'Nhim trng h hp nh',
      notes: 'K toa khng sinh, ngh ngi nhiu, ung nhiu nc',
      nextAppointment: '15/04/2025'
    },
    {
      id: 3,
      date: '15/02/2025',
      doctor: 'BS. Nguyn Vn A',
      diagnosis: 'Ti khm nh k',
      notes: 'iu chnh liu thuc ARV, xt nghim CD4 cho kt qu tt',
      nextAppointment: '15/03/2025'
    },
  ]);

  // M phng d liu thuc ang dng
  const [medications] = useState([
    {
      id: 1,
      name: 'Efavirenz 600mg',
      dosage: '1 vin/ngy',
      schedule: 'Ti trc khi i ng',
      startDate: '10/01/2025',
      endDate: 'Dng lin tc'
    },
    {
      id: 2,
      name: 'Lamivudine 300mg',
      dosage: '1 vin/ngy',
      schedule: 'Sng sau khi n',
      startDate: '10/01/2025',
      endDate: 'Dng lin tc'
    },
    {
      id: 3,
      name: 'Tenofovir 300mg',
      dosage: '1 vin/ngy',
      schedule: 'Sng sau khi n',
      startDate: '10/01/2025',
      endDate: 'Dng lin tc'
    }
  ]);

  // M phng d liu thng bo t h thng
  const [notifications] = useState([
    {
      id: 1,
      date: '10/05/2025',
      title: 'Nhc lch hn',
      message: 'Bn c lch hn ti khm vo ngy 15/05/2025',
      isRead: false
    },
    {
      id: 2,
      date: '05/05/2025',
      title: 'Nhc ung thuc',
      message: 'Hy m bo bn ung thuc y  theo lch, khng b liu',
      isRead: true
    },
    {
      id: 3,
      date: '01/05/2025',
      title: 'Kt qu xt nghim',
      message: 'Kt qu xt nghim mi nht ca bn  c sn',
      isRead: true
    }
  ]);

  useEffect(() => {
    if (!currentUser) {
      navigate('/login', { state: { from: location } });
    }
  }, [currentUser, navigate, location]);

  useEffect(() => {
    if (notification) {
      const timer = setTimeout(() => {
        setNotification(null);
      }, 5000);
      
      return () => clearTimeout(timer);
    }
  }, [notification]);

  const handleTabChange = (tab) => {
    setActiveTab(tab);
  };

  const handleEditToggle = () => {
    setEditMode(!editMode);
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    try {
      const success = await updateProfile(formData);
      
      if (success) {
        setNotification({
          type: 'success',
          message: 'Cp nht thng tin thnh cng!'
        });
        setEditMode(false);
      } else {
        setNotification({
          type: 'error',
          message: 'Khng th cp nht thng tin'
        });
      }
    } catch (err) {
      setNotification({
        type: 'error',
        message: ' xy ra li khi cp nht thng tin'
      });
      console.error(err);
    }
  };

  const handleLogout = () => {
    logout();
    navigate('/login');
  };

  return (
    <>
      <Navbar />
      <div className="profile-container">
        <div className="profile-sidebar">
          <div className="profile-user-info">
            <div className="profile-avatar">
              <img src="/avatar-placeholder.jpg" alt="Avatar" />
              {editMode && (
                <button className="change-avatar-btn">
                  <i className="fas fa-camera"></i>
                </button>
              )}
            </div>
            <h3>{currentUser?.name || 'Ngi dng'}</h3>
            <p>{currentUser?.email || 'email@example.com'}</p>
          </div>
          
          <div className="profile-nav">
            <button 
              className={`profile-nav-btn ${activeTab === 'personal' ? 'active' : ''}`}
              onClick={() => handleTabChange('personal')}
            >
              <i className="fas fa-user"></i>
              <span>Thng tin c nhn</span>
            </button>
            
            <button 
              className={`profile-nav-btn ${activeTab === 'medical' ? 'active' : ''}`}
              onClick={() => handleTabChange('medical')}
            >
              <i className="fas fa-notes-medical"></i>
              <span>H s bnh n</span>
            </button>
            
            <button 
              className={`profile-nav-btn ${activeTab === 'medication' ? 'active' : ''}`}
              onClick={() => handleTabChange('medication')}
            >
              <i className="fas fa-pills"></i>
              <span>Thuc ang dng</span>
            </button>
            
            <button 
              className={`profile-nav-btn ${activeTab === 'notifications' ? 'active' : ''}`}
              onClick={() => handleTabChange('notifications')}
            >
              <i className="fas fa-bell"></i>
              <span>Thng bo</span>
              <span className="notification-badge">1</span>
            </button>
          </div>
          
          <div className="profile-actions">
            <Link to="/appointments" className="profile-action-btn appointments">
              <i className="fas fa-calendar-check"></i>
              <span>t lch khm</span>
            </Link>
            
            <button className="profile-action-btn logout" onClick={handleLogout}>
              <i className="fas fa-sign-out-alt"></i>
              <span>ng xut</span>
            </button>
          </div>
        </div>
        
        <div className="profile-content">
          {notification && (
            <div className={`profile-notification ${notification.type}`}>
              <i className={`fas ${notification.type === 'success' ? 'fa-check-circle' : 'fa-exclamation-circle'}`}></i>
              <span>{notification.message}</span>
              <button onClick={() => setNotification(null)}>
                <i className="fas fa-times"></i>
              </button>
            </div>
          )}
          
          <div className="profile-header">
            <h2>
              {activeTab === 'personal' && 'Thng tin c nhn'}
              {activeTab === 'medical' && 'H s bnh n'}
              {activeTab === 'medication' && 'Thuc ang dng'}
              {activeTab === 'notifications' && 'Thng bo h thng'}
            </h2>
            
            {activeTab === 'personal' && (
              <button 
                className={`edit-profile-btn ${editMode ? 'active' : ''}`}
                onClick={handleEditToggle}
              >
                {editMode ? (
                  <>
                    <i className="fas fa-times"></i>
                    <span>Hy chnh sa</span>
                  </>
                ) : (
                  <>
                    <i className="fas fa-edit"></i>
                    <span>Chnh sa</span>
                  </>
                )}
              </button>
            )}
          </div>
          
          <div className="profile-body">
            {activeTab === 'personal' && (
              <form onSubmit={handleSubmit} className="personal-info-form">
                <div className="form-section">
                  <h3>Thng tin c bn</h3>
                  <div className="form-row">
                    <div className="form-group">
                      <label htmlFor="fullName">H v tn</label>
                      <input
                        type="text"
                        id="fullName"
                        name="fullName"
                        value={formData.fullName}
                        onChange={handleChange}
                        disabled={!editMode}
                        required
                      />
                    </div>
                    
                    <div className="form-group">
                      <label htmlFor="email">Email</label>
                      <input
                        type="email"
                        id="email"
                        name="email"
                        value={formData.email}
                        onChange={handleChange}
                        disabled={true}
                        required
                      />
                    </div>
                  </div>
                  
                  <div className="form-row">
                    <div className="form-group">
                      <label htmlFor="phoneNumber">S in thoi</label>
                      <input
                        type="tel"
                        id="phoneNumber"
                        name="phoneNumber"
                        value={formData.phoneNumber}
                        onChange={handleChange}
                        disabled={!editMode}
                      />
                    </div>
                    
                    <div className="form-group">
                      <label htmlFor="dateOfBirth">Ngy sinh</label>
                      <input
                        type="date"
                        id="dateOfBirth"
                        name="dateOfBirth"
                        value={formData.dateOfBirth}
                        onChange={handleChange}
                        disabled={!editMode}
                      />
                    </div>
                  </div>
                  
                  <div className="form-row">
                    <div className="form-group">
                      <label htmlFor="gender">Gii tnh</label>
                      <select
                        id="gender"
                        name="gender"
                        value={formData.gender}
                        onChange={handleChange}
                        disabled={!editMode}
                      >
                        <option value="">Chn gii tnh</option>
                        <option value="male">Nam</option>
                        <option value="female">N</option>
                        <option value="other">Khc</option>
                      </select>
                    </div>
                    
                    <div className="form-group">
                      <label htmlFor="address">a ch</label>
                      <input
                        type="text"
                        id="address"
                        name="address"
                        value={formData.address}
                        onChange={handleChange}
                        disabled={!editMode}
                      />
                    </div>
                  </div>
                </div>
                
                <div className="form-section">
                  <h3>Thng tin y t</h3>
                  <div className="form-row">
                    <div className="form-group">
                      <label htmlFor="bloodType">Nhm mu</label>
                      <select
                        id="bloodType"
                        name="bloodType"
                        value={formData.bloodType}
                        onChange={handleChange}
                        disabled={!editMode}
                      >
                        <option value="">Chn nhm mu</option>
                        <option value="A+">A+</option>
                        <option value="A-">A-</option>
                        <option value="B+">B+</option>
                        <option value="B-">B-</option>
                        <option value="AB+">AB+</option>
                        <option value="AB-">AB-</option>
                        <option value="O+">O+</option>
                        <option value="O-">O-</option>
                      </select>
                    </div>
                    
                    <div className="form-group">
                      <label htmlFor="emergencyContact">Lin h khn cp</label>
                      <input
                        type="text"
                        id="emergencyContact"
                        name="emergencyContact"
                        value={formData.emergencyContact}
                        onChange={handleChange}
                        disabled={!editMode}
                        placeholder="Tn & s in thoi"
                      />
                    </div>
                  </div>
                  
                  <div className="form-row vertical">
                    <div className="form-group">
                      <label htmlFor="allergies">D ng</label>
                      <textarea
                        id="allergies"
                        name="allergies"
                        value={formData.allergies}
                        onChange={handleChange}
                        disabled={!editMode}
                        placeholder="Lit k cc loi d ng (nu c)"
                      ></textarea>
                    </div>
                  </div>
                  
                  <div className="form-row vertical">
                    <div className="form-group">
                      <label htmlFor="currentMedications">Thuc ang s dng (ngoi phc  HIV)</label>
                      <textarea
                        id="currentMedications"
                        name="currentMedications"
                        value={formData.currentMedications}
                        onChange={handleChange}
                        disabled={!editMode}
                        placeholder="Lit k cc loi thuc ang s dng ngoi phc  iu tr HIV"
                      ></textarea>
                    </div>
                  </div>
                </div>
                
                {editMode && (
                  <div className="form-actions">
                    <button 
                      type="button" 
                      className="cancel-btn"
                      onClick={handleEditToggle}
                    >
                      Hy
                    </button>
                    <button 
                      type="submit" 
                      className="save-btn"
                    >
                      Lu thay i
                    </button>
                  </div>
                )}
              </form>
            )}
            
            {activeTab === 'medical' && (
              <div className="medical-history">
                <div className="section-info">
                  <p>Hin th lch s khm bnh v iu tr gn y.  xem ton b h s, vui lng lin h phng khm.</p>
                </div>
                
                <div className="medical-history-list">
                  {medicalHistory.map(record => (
                    <div className="medical-record" key={record.id}>
                      <div className="record-header">
                        <div className="record-date">{record.date}</div>
                        <div className="record-doctor">{record.doctor}</div>
                      </div>
                      
                      <div className="record-body">
                        <div className="record-item">
                          <span className="label">Chn on:</span>
                          <span className="value">{record.diagnosis}</span>
                        </div>
                        
                        <div className="record-item">
                          <span className="label">Ghi ch:</span>
                          <span className="value">{record.notes}</span>
                        </div>
                        
                        <div className="record-item">
                          <span className="label">Lch hn tip theo:</span>
                          <span className="value highlight">{record.nextAppointment}</span>
                        </div>
                      </div>
                      
                      <div className="record-actions">
                        <button className="record-btn">
                          <i className="fas fa-file-pdf"></i>
                          <span>Xem toa thuc</span>
                        </button>
                        
                        <button className="record-btn">
                          <i className="fas fa-file-medical-alt"></i>
                          <span>Xem kt qu xt nghim</span>
                        </button>
                      </div>
                    </div>
                  ))}
                </div>
                
                <div className="view-all-btn-container">
                  <button className="view-all-btn">
                    <span>Xem tt c lch s khm bnh</span>
                    <i className="fas fa-arrow-right"></i>
                  </button>
                </div>
              </div>
            )}
            
            {activeTab === 'medication' && (
              <div className="medication-list">
                <div className="section-info">
                  <p>Danh sch thuc bn ang s dng theo phc  iu tr hin ti. Hy m bo dng thuc ng liu, ng gi.</p>
                </div>
                
                <div className="current-medications">
                  {medications.map(med => (
                    <div className="medication-card" key={med.id}>
                      <div className="medication-header">
                        <h4>{med.name}</h4>
                        <span className="medication-badge">ang dng</span>
                      </div>
                      
                      <div className="medication-details">
                        <div className="medication-detail">
                          <i className="fas fa-prescription-bottle"></i>
                          <span className="label">Liu lng:</span>
                          <span className="value">{med.dosage}</span>
                        </div>
                        
                        <div className="medication-detail">
                          <i className="fas fa-clock"></i>
                          <span className="label">Lch ung:</span>
                          <span className="value">{med.schedule}</span>
                        </div>
                        
                        <div className="medication-detail">
                          <i className="fas fa-calendar-plus"></i>
                          <span className="label">Ngy bt u:</span>
                          <span className="value">{med.startDate}</span>
                        </div>
                        
                        <div className="medication-detail">
                          <i className="fas fa-calendar-minus"></i>
                          <span className="label">Ngy kt thc:</span>
                          <span className="value">{med.endDate}</span>
                        </div>
                      </div>
                      
                      <div className="medication-actions">
                        <button className="medication-btn info">
                          <i className="fas fa-info-circle"></i>
                          <span>Thng tin thuc</span>
                        </button>
                        
                        <button className="medication-btn reminder">
                          <i className="fas fa-bell"></i>
                          <span>Thit lp nhc nh</span>
                        </button>
                      </div>
                    </div>
                  ))}
                </div>
                
                <div className="medication-links">
                  <Link to="/medication" className="medication-link">
                    <i className="fas fa-pills"></i>
                    <span>Qun l thuc v nhc nh</span>
                  </Link>
                  
                  <Link to="/resources" className="medication-link">
                    <i className="fas fa-book-medical"></i>
                    <span>Thng tin v thuc ARV</span>
                  </Link>
                </div>
              </div>
            )}
            
            {activeTab === 'notifications' && (
              <div className="notification-list">
                <div className="section-info">
                  <p>Cc thng bo t h thng lin quan n lch khm, thuc v cc thng tin quan trng khc.</p>
                </div>
                
                <div className="notifications">
                  {notifications.map(notification => (
                    <div className={`notification-item ${!notification.isRead ? 'unread' : ''}`} key={notification.id}>
                      <div className="notification-icon">
                        {notification.title.includes('lch hn') && <i className="fas fa-calendar-check"></i>}
                        {notification.title.includes('thuc') && <i className="fas fa-pills"></i>}
                        {notification.title.includes('xt nghim') && <i className="fas fa-vial"></i>}
                      </div>
                      
                      <div className="notification-content">
                        <div className="notification-header">
                          <h4>{notification.title}</h4>
                          <span className="notification-date">{notification.date}</span>
                        </div>
                        <p>{notification.message}</p>
                      </div>
                      
                      <div className="notification-actions">
                        <button className="mark-read-btn">
                          <i className="fas fa-check"></i>
                        </button>
                        <button className="delete-notification-btn">
                          <i className="fas fa-trash"></i>
                        </button>
                      </div>
                    </div>
                  ))}
                </div>
                
                <div className="notification-settings">
                  <button className="notification-settings-btn">
                    <i className="fas fa-cog"></i>
                    <span>Ci t thng bo</span>
                  </button>
                  
                  <button className="mark-all-read-btn">
                    <i className="fas fa-check-double"></i>
                    <span>nh du tt c  c</span>
                  </button>
                </div>
              </div>
            )}
          </div>
        </div>
      </div>
    </>
  );
};

export default Profile;