import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';
import Navbar from '../components/Navbar';
import '../styles/ForgotPassword.css';

const ForgotPassword = () => {
  const [email, setEmail] = useState('');
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [message, setMessage] = useState('');
  const [messageType, setMessageType] = useState('');
  
  const { resetPassword, error } = useAuth();

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (!email) {
      setMessage('Vui lng nhp a ch email');
      setMessageType('error');
      return;
    }
    
    setIsSubmitting(true);
    setMessage('');
    
    try {
      const success = await resetPassword(email);
      
      if (success) {
        setMessage('Hng dn t li mt khu  c gi n email ca bn');
        setMessageType('success');
        setEmail('');
      } else {
        setMessage(error || 'Khng th gi yu cu t li mt khu');
        setMessageType('error');
      }
    } catch (err) {
      setMessage(' xy ra li khi gi yu cu');
      setMessageType('error');
      console.error(err);
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <>
      <Navbar />
      <div className="forgot-password-container">
        <div className="forgot-password-card">
          <div className="forgot-password-header">
            <h2>Qun mt khu</h2>
            <p>Nhp email ca bn  nhn hng dn t li mt khu</p>
          </div>
          
          {message && (
            <div className={`forgot-password-message ${messageType}`}>
              {message}
            </div>
          )}
          
          <form onSubmit={handleSubmit} className="forgot-password-form">
            <div className="form-group">
              <label htmlFor="email">Email</label>
              <div className="input-with-icon">
                <i className="fas fa-envelope"></i>
                <input
                  type="email"
                  id="email"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                  placeholder="Nhp a ch email  ng k"
                  required
                />
              </div>
            </div>
            
            <button 
              type="submit" 
              className="reset-button" 
              disabled={isSubmitting}
            >
              {isSubmitting ? 'ang x l...' : 'Gi yu cu t li mt khu'}
            </button>
          </form>
          
          <div className="forgot-password-footer">
            <div className="footer-link">
              <i className="fas fa-arrow-left"></i>
              <Link to="/login">Quay li trang ng nhp</Link>
            </div>
            <div className="footer-link">
              <i className="fas fa-question-circle"></i>
              <Link to="/help">Cn tr gip?</Link>
            </div>
          </div>
          
          <div className="support-info">
            <p>Bn gp vn  khi t li mt khu?</p>
            <p>Lin h h tr: <a href="mailto:support@hivhospital.vn">support@hivhospital.vn</a> hoc gi <a href="tel:+84123456789">0123 456 789</a></p>
          </div>
        </div>
      </div>
    </>
  );
};

export default ForgotPassword;