import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import Navbar from '../components/Navbar';
import '../styles/HospitalHome.css';

const HospitalHome = () => {
  const [currentSlide, setCurrentSlide] = useState(0);
  const [news, setNews] = useState([]);
  const [doctors, setDoctors] = useState([]);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    // M phng vic ti d liu t API
    const fetchData = async () => {
      setIsLoading(true);
      try {
        // Trong d n thc t, bn s gi API thc s
        setTimeout(() => {
          setNews([
            {
              id: 1,
              title: 'K nim Ngy Th gii phng chng HIV/AIDS',
              image: '/news-1.jpg',
              date: '01/12/2024',
              summary: 'Bnh vin t chc chui hot ng nng cao nhn thc cng ng v HIV/AIDS...'
            },
            {
              id: 2,
              title: 'Thm phc  iu tr HIV mi c ph duyt',
              image: '/news-2.jpg',
              date: '15/10/2024',
              summary: 'B Y t ph duyt phc  iu tr HIV mi, h tr bnh nhn gim tc dng ph...'
            },
            {
              id: 3,
              title: 'Chng trnh t vn dinh dng cho ngi nhim HIV',
              image: '/news-3.jpg',
              date: '05/09/2024',
              summary: 'Bnh vin trin khai chng trnh t vn dinh dng min ph cho bnh nhn HIV...'
            },
          ]);

          setDoctors([
            {
              id: 1,
              name: 'BS. Nguyn Vn A',
              specialty: 'Trng khoa HIV/AIDS',
              image: '/doctor-1.jpg',
              experience: '15 nm kinh nghim'
            },
            {
              id: 2,
              name: 'BS. Phm Th B',
              specialty: 'Bc s iu tr HIV',
              image: '/doctor-2.jpg',
              experience: '10 nm kinh nghim'
            },
            {
              id: 3,
              name: 'BS. Trn Vn C',
              specialty: 'Chuyn gia t vn HIV',
              image: '/doctor-3.jpg',
              experience: '12 nm kinh nghim'
            },
          ]);
          
          setIsLoading(false);
        }, 1000);

      } catch (error) {
        console.error('Li khi ti d liu:', error);
        setIsLoading(false);
      }
    };

    fetchData();

    // Auto slideshow
    const slideInterval = setInterval(() => {
      setCurrentSlide((prev) => (prev === 2 ? 0 : prev + 1));
    }, 5000);

    return () => clearInterval(slideInterval);
  }, []);

  const slides = [
    {
      id: 1,
      image: '/slide-1.jpg',
      title: 'Chm sc ton din cho ngi nhim HIV',
      description: 'H thng y t cht lng cao vi i ng bc s giu kinh nghim',
      link: '/hospital/dich-vu'
    },
    {
      id: 2,
      image: '/slide-2.jpg',
      title: 'Phc  iu tr HIV hin i',
      description: 'p dng cc phng php tin tin nht trong iu tr HIV/AIDS',
      link: '/hospital/chuyen-khoa'
    },
    {
      id: 3,
      image: '/slide-3.jpg',
      title: 't lch khm trc tuyn',
      description: 'D dng t lch khm ch trong vi pht, khng cn ch i',
      link: '/hospital/lich-kham'
    }
  ];

  const goToSlide = (index) => {
    setCurrentSlide(index);
  };

  const nextSlide = () => {
    setCurrentSlide((prev) => (prev === slides.length - 1 ? 0 : prev + 1));
  };

  const prevSlide = () => {
    setCurrentSlide((prev) => (prev === 0 ? slides.length - 1 : prev - 1));
  };

  if (isLoading) {
    return (
      <div className="loading-container">
        <div className="spinner"></div>
        <p>ang ti d liu...</p>
      </div>
    );
  }

  return (
    <div className="hospital-home">
      <Navbar />
      
      {/* Hero Slider */}
      <div className="hero-slider">
        <div className="slides-container">
          {slides.map((slide, index) => (
            <div 
              key={slide.id} 
              className={`slide ${index === currentSlide ? 'active' : ''}`}
              style={{ backgroundImage: `url(${slide.image})` }}
            >
              <div className="slide-content">
                <h2>{slide.title}</h2>
                <p>{slide.description}</p>
                <Link to={slide.link} className="slide-btn">Tm hiu thm</Link>
              </div>
            </div>
          ))}
        </div>
        
        <button className="slider-btn prev" onClick={prevSlide}>
          <i className="fas fa-chevron-left"></i>
        </button>
        <button className="slider-btn next" onClick={nextSlide}>
          <i className="fas fa-chevron-right"></i>
        </button>
        
        <div className="slider-dots">
          {slides.map((_, index) => (
            <button 
              key={index} 
              className={`dot ${index === currentSlide ? 'active' : ''}`}
              onClick={() => goToSlide(index)}
            ></button>
          ))}
        </div>
      </div>
      
      {/* Services Section */}
      <section className="services-section">
        <div className="container">
          <div className="section-header">
            <h2>Dch v ca chng ti</h2>
            <p>Cung cp cc dch v y t cht lng cao cho bnh nhn HIV/AIDS</p>
          </div>
          
          <div className="services-grid">
            <div className="service-card">
              <div className="service-icon">
                <i className="fas fa-user-md"></i>
              </div>
              <h3>T vn HIV/AIDS</h3>
              <p>Dch v t vn trc v sau xt nghim HIV t i ng chuyn gia tm l.</p>
              <Link to="/hospital/dich-vu/tu-van" className="read-more">Xem thm</Link>
            </div>
            
            <div className="service-card">
              <div className="service-icon">
                <i className="fas fa-pills"></i>
              </div>
              <h3>iu tr ARV</h3>
              <p>Cung cp phc  iu tr ARV hin i gip kim sot virus HIV hiu qu.</p>
              <Link to="/hospital/dich-vu/dieu-tri-arv" className="read-more">Xem thm</Link>
            </div>
            
            <div className="service-card">
              <div className="service-icon">
                <i className="fas fa-vial"></i>
              </div>
              <h3>Xt nghim HIV</h3>
              <p>Xt nghim nhanh, chnh xc v bo mt vi cng ngh tin tin.</p>
              <Link to="/hospital/dich-vu/xet-nghiem" className="read-more">Xem thm</Link>
            </div>
            
            <div className="service-card">
              <div className="service-icon">
                <i className="fas fa-heartbeat"></i>
              </div>
              <h3>iu tr bnh ng nhim</h3>
              <p>iu tr ton din cc bnh ng nhim nh vim gan, lao, STIs...</p>
              <Link to="/hospital/dich-vu/benh-dong-nhiem" className="read-more">Xem thm</Link>
            </div>
          </div>
        </div>
      </section>
      
      {/* Doctor Section */}
      <section className="doctors-section">
        <div className="container">
          <div className="section-header">
            <h2>i ng bc s</h2>
            <p>Cc chuyn gia y t giu kinh nghim trong lnh vc HIV/AIDS</p>
          </div>
          
          <div className="doctors-grid">
            {doctors.map(doctor => (
              <div className="doctor-card" key={doctor.id}>
                <div className="doctor-image">
                  <img src={doctor.image} alt={doctor.name} />
                </div>
                <div className="doctor-info">
                  <h3>{doctor.name}</h3>
                  <p className="specialty">{doctor.specialty}</p>
                  <p className="experience">{doctor.experience}</p>
                  <div className="doctor-contact">
                    <Link to={`/hospital/bac-si/${doctor.id}`} className="doctor-btn">Xem h s</Link>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
      </section>
      
      {/* News Section */}
      <section className="news-section">
        <div className="container">
          <div className="section-header">
            <h2>Tin tc - S kin</h2>
            <p>Cp nht thng tin mi nht v HIV/AIDS v hot ng ca bnh vin</p>
          </div>
          
          <div className="news-grid">
            {news.map(item => (
              <div className="news-card" key={item.id}>
                <div className="news-image">
                  <img src={item.image} alt={item.title} />
                  <div className="news-date">{item.date}</div>
                </div>
                <div className="news-content">
                  <h3>{item.title}</h3>
                  <p>{item.summary}</p>
                  <Link to={`/hospital/tin-tuc/${item.id}`} className="read-more">c tip</Link>
                </div>
              </div>
            ))}
          </div>
          
          <div className="view-all-news">
            <Link to="/hospital/tin-tuc" className="view-all-btn">Xem tt c tin tc</Link>
          </div>
        </div>
      </section>
      
      {/* Appointment Section */}
      <section className="appointment-section">
        <div className="container">
          <div className="appointment-grid">
            <div className="appointment-info">
              <h2>t lch khm</h2>
              <p>t lch khm trc tuyn  tit kim thi gian v nhn c s chm sc tt nht.</p>
              <ul className="appointment-benefits">
                <li>
                  <i className="fas fa-check-circle"></i>
                  <span>D dng t lch ch trong vi pht</span>
                </li>
                <li>
                  <i className="fas fa-check-circle"></i>
                  <span>La chn bc s v thi gian ph hp</span>
                </li>
                <li>
                  <i className="fas fa-check-circle"></i>
                  <span>Nhn thng bo nhc lch khm qua email hoc SMS</span>
                </li>
                <li>
                  <i className="fas fa-check-circle"></i>
                  <span>H tr trc tuyn t i ng nhn vin chm sc khch hng</span>
                </li>
              </ul>
              <Link to="/hospital/lich-kham" className="appointment-btn">t lch ngay</Link>
            </div>
            
            <div className="appointment-image">
              <img src="/appointment-banner.jpg" alt="t lch khm" />
            </div>
          </div>
        </div>
      </section>
      
      {/* Statistics Section */}
      <section className="statistics-section">
        <div className="container">
          <div className="statistics-grid">
            <div className="statistic-item">
              <div className="statistic-icon">
                <i className="fas fa-user-md"></i>
              </div>
              <div className="statistic-number">50+</div>
              <div className="statistic-title">Bc s chuyn khoa</div>
            </div>
            
            <div className="statistic-item">
              <div className="statistic-icon">
                <i className="fas fa-procedures"></i>
              </div>
              <div className="statistic-number">10.000+</div>
              <div className="statistic-title">Bnh nhn iu tr</div>
            </div>
            
            <div className="statistic-item">
              <div className="statistic-icon">
                <i className="fas fa-hospital"></i>
              </div>
              <div className="statistic-number">15+</div>
              <div className="statistic-title">Nm kinh nghim</div>
            </div>
            
            <div className="statistic-item">
              <div className="statistic-icon">
                <i className="fas fa-award"></i>
              </div>
              <div className="statistic-number">20+</div>
              <div className="statistic-title">Gii thng</div>
            </div>
          </div>
        </div>
      </section>
      
      {/* Testimonials Section */}
      <section className="testimonials-section">
        <div className="container">
          <div className="section-header">
            <h2> kin bnh nhn</h2>
            <p>Nhng chia s t bnh nhn  iu tr ti bnh vin</p>
          </div>
          
          <div className="testimonials-wrapper">
            <div className="testimonial-card">
              <div className="testimonial-rating">
                <i className="fas fa-star"></i>
                <i className="fas fa-star"></i>
                <i className="fas fa-star"></i>
                <i className="fas fa-star"></i>
                <i className="fas fa-star"></i>
              </div>
              <p className="testimonial-content">
                "Ti  c chn on nhim HIV t 5 nm trc v c iu tr ti y. Ti rt hi lng vi s chm sc tn tnh ca i ng y bc s. Gi y sc khe ca ti  n nh v ti c th sng mt cuc sng bnh thng."
              </p>
              <div className="testimonial-author">
                <div className="author-image">
                  <img src="/testimonial-1.jpg" alt="Nguyn Vn X" />
                </div>
                <div className="author-info">
                  <h4>Nguyn Vn X</h4>
                  <p>Bnh nhn iu tr 5 nm</p>
                </div>
              </div>
            </div>
            
            <div className="testimonial-card">
              <div className="testimonial-rating">
                <i className="fas fa-star"></i>
                <i className="fas fa-star"></i>
                <i className="fas fa-star"></i>
                <i className="fas fa-star"></i>
                <i className="fas fa-star"></i>
              </div>
              <p className="testimonial-content">
                "Phng khm c quy trnh bo mt thng tin rt tt, iu ny gip ti an tm khi n khm v iu tr. Cc bc s t vn rt tn tm v gip ti hiu r v tnh trng sc khe cng nh cch iu tr hiu qu."
              </p>
              <div className="testimonial-author">
                <div className="author-image">
                  <img src="/testimonial-2.jpg" alt="Trn Th Y" />
                </div>
                <div className="author-info">
                  <h4>Trn Th Y</h4>
                  <p>Bnh nhn iu tr 3 nm</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
      
      {/* Contact Section */}
      <section className="contact-section">
        <div className="container">
          <div className="contact-grid">
            <div className="contact-info">
              <h2>Lin h vi chng ti</h2>
              <p>Hy lin h nu bn c bt k cu hi no v dch v ca chng ti.</p>
              
              <div className="contact-details">
                <div className="contact-item">
                  <div className="contact-icon">
                    <i className="fas fa-map-marker-alt"></i>
                  </div>
                  <div className="contact-text">
                    <h4>a ch</h4>
                    <p>78 Gii Phng, Phng Mai, ng a, H Ni</p>
                  </div>
                </div>
                
                <div className="contact-item">
                  <div className="contact-icon">
                    <i className="fas fa-phone-alt"></i>
                  </div>
                  <div className="contact-text">
                    <h4>Hotline</h4>
                    <p>1900-6889</p>
                  </div>
                </div>
                
                <div className="contact-item">
                  <div className="contact-icon">
                    <i className="fas fa-envelope"></i>
                  </div>
                  <div className="contact-text">
                    <h4>Email</h4>
                    <p>info@hivhospital.vn</p>
                  </div>
                </div>
                
                <div className="contact-item">
                  <div className="contact-icon">
                    <i className="fas fa-clock"></i>
                  </div>
                  <div className="contact-text">
                    <h4>Gi lm vic</h4>
                    <p>Th 2 - Th 6: 7:30 - 17:00</p>
                    <p>Th 7: 7:30 - 12:00</p>
                  </div>
                </div>
              </div>
              
              <div className="social-links">
                <a href="#" className="social-link">
                  <i className="fab fa-facebook-f"></i>
                </a>
                <a href="#" className="social-link">
                  <i className="fab fa-youtube"></i>
                </a>
                <a href="#" className="social-link">
                  <i className="fab fa-twitter"></i>
                </a>
                <a href="#" className="social-link">
                  <i className="fab fa-linkedin-in"></i>
                </a>
              </div>
            </div>
            
            <div className="contact-form-container">
              <form className="contact-form">
                <div className="form-group">
                  <label htmlFor="name">H tn</label>
                  <input type="text" id="name" name="name" required />
                </div>
                
                <div className="form-group">
                  <label htmlFor="email">Email</label>
                  <input type="email" id="email" name="email" required />
                </div>
                
                <div className="form-group">
                  <label htmlFor="phone">S in thoi</label>
                  <input type="tel" id="phone" name="phone" required />
                </div>
                
                <div className="form-group">
                  <label htmlFor="subject">Ch </label>
                  <input type="text" id="subject" name="subject" required />
                </div>
                
                <div className="form-group">
                  <label htmlFor="message">Ni dung</label>
                  <textarea id="message" name="message" rows="5" required></textarea>
                </div>
                
                <button type="submit" className="submit-btn">Gi tin nhn</button>
              </form>
            </div>
          </div>
        </div>
      </section>
      
      {/* Footer */}
      <footer className="hospital-footer">
        <div className="container">
          <div className="footer-top">
            <div className="footer-column">
              <div className="footer-logo">
                <img src="/hospital-logo.png" alt="Bnh vin Logo" />
                <span>BNH VIN IU TR HIV</span>
              </div>
              <p className="footer-desc">
                Bnh vin chuyn iu tr HIV/AIDS hng u Vit Nam, cung cp dch v chm sc y t ton din v cht lng cao.
              </p>
            </div>
            
            <div className="footer-column">
              <h3>Lin kt nhanh</h3>
              <ul className="footer-links">
                <li><Link to="/hospital">Trang ch</Link></li>
                <li><Link to="/hospital/gioi-thieu">Gii thiu</Link></li>
                <li><Link to="/hospital/chuyen-khoa">Chuyn khoa</Link></li>
                <li><Link to="/hospital/lich-kham">t lch khm</Link></li>
                <li><Link to="/hospital/tin-tuc">Tin tc</Link></li>
                <li><Link to="/hospital/lien-he">Lin h</Link></li>
              </ul>
            </div>
            
            <div className="footer-column">
              <h3>Dch v</h3>
              <ul className="footer-links">
                <li><Link to="/hospital/dich-vu/tu-van">T vn HIV/AIDS</Link></li>
                <li><Link to="/hospital/dich-vu/dieu-tri-arv">iu tr ARV</Link></li>
                <li><Link to="/hospital/dich-vu/xet-nghiem">Xt nghim HIV</Link></li>
                <li><Link to="/hospital/dich-vu/benh-dong-nhiem">iu tr bnh ng nhim</Link></li>
                <li><Link to="/hospital/dich-vu/dinh-duong">T vn dinh dng</Link></li>
              </ul>
            </div>
            
            <div className="footer-column">
              <h3>Lin h</h3>
              <ul className="footer-contact">
                <li>
                  <i className="fas fa-map-marker-alt"></i>
                  <span>78 Gii Phng, Phng Mai, ng a, H Ni</span>
                </li>
                <li>
                  <i className="fas fa-phone-alt"></i>
                  <span>1900-6889</span>
                </li>
                <li>
                  <i className="fas fa-envelope"></i>
                  <span>info@hivhospital.vn</span>
                </li>
              </ul>
              
              <div className="footer-social">
                <a href="#" className="social-icon">
                  <i className="fab fa-facebook-f"></i>
                </a>
                <a href="#" className="social-icon">
                  <i className="fab fa-youtube"></i>
                </a>
                <a href="#" className="social-icon">
                  <i className="fab fa-twitter"></i>
                </a>
                <a href="#" className="social-icon">
                  <i className="fab fa-linkedin-in"></i>
                </a>
              </div>
            </div>
          </div>
          
          <div className="footer-bottom">
            <p>&copy; 2025 Bnh vin iu tr HIV. Tt c quyn c bo lu.</p>
            <ul className="footer-legal">
              <li><Link to="/hospital/chinh-sach-bao-mat">Chnh sch bo mt</Link></li>
              <li><Link to="/hospital/dieu-khoan-su-dung">iu khon s dng</Link></li>
            </ul>
          </div>
        </div>
      </footer>
    </div>
  );
};

export default HospitalHome;