import React from 'react';
import { Link } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';
import Navbar from '../components/Navbar';
import '../styles/Home.css';

const Home = () => {
  const { currentUser } = useAuth();

  return (
    <>
      <Navbar />
      <div className="home-container">
        {/* Hero Section */}
        <section className="hero-section">
          <div className="hero-content">
            <h1>Qun l iu tr HIV mt cch hiu qu</h1>
            <p>ng dng ton din gip bn theo di iu tr, qun l lch hn v kt ni vi i ng y t</p>
            
            <div className="hero-buttons">
              {currentUser ? (
                <Link to="/profile" className="primary-button">
                  <i className="fas fa-user-circle"></i>
                  H s ca ti
                </Link>
              ) : (
                <Link to="/login" className="primary-button">
                  <i className="fas fa-sign-in-alt"></i>
                  ng nhp
                </Link>
              )}
              <Link to="/hospital" className="secondary-button">
                <i className="fas fa-hospital"></i>
                Trang bnh vin
              </Link>
            </div>
          </div>
          <div className="hero-image">
            <img src="/hero-image.jpg" alt="HIV Treatment App" />
          </div>
        </section>
        
        {/* Features Section */}
        <section className="features-section">
          <div className="section-header">
            <h2>Tnh nng ni bt</h2>
            <p>ng dng cung cp cc cng c cn thit  qun l vic iu tr HIV ca bn</p>
          </div>
          
          <div className="features-container">
            <div className="feature-card">
              <div className="feature-icon">
                <img src="/assets/images/medication.png" alt="Medication Icon" />
              </div>
              <h3>Qun l thuc</h3>
              <p>Theo di lch ung thuc, nhn thng bo nhc nh v qun l toa thuc ca bn</p>
              <Link to="/medication" className="feature-link">
                Xem chi tit
                <i className="fas fa-arrow-right"></i>
              </Link>
            </div>
            
            <div className="feature-card">
              <div className="feature-icon">
                <img src="/assets/images/appointment.png" alt="Appointment Icon" />
              </div>
              <h3>t lch khm</h3>
              <p>t lch hn trc tuyn, nhn thng bo v theo di cc cuc hn sp ti</p>
              <Link to="/appointments" className="feature-link">
                Xem chi tit
                <i className="fas fa-arrow-right"></i>
              </Link>
            </div>
            
            <div className="feature-card">
              <div className="feature-icon">
                <img src="/assets/images/treatment.png" alt="Treatment Icon" />
              </div>
              <h3>K hoch iu tr</h3>
              <p>Xem v theo di k hoch iu tr c nhn do bc s ca bn  xut</p>
              <Link to="/treatment-plan" className="feature-link">
                Xem chi tit
                <i className="fas fa-arrow-right"></i>
              </Link>
            </div>
          </div>
        </section>
        
        {/* How It Works Section */}
        <section className="how-it-works-section">
          <div className="section-header">
            <h2>Cch thc hot ng</h2>
            <p>Ba bc n gin  bt u qun l vic iu tr HIV ca bn</p>
          </div>
          
          <div className="steps-container">
            <div className="step-item">
              <div className="step-number">1</div>
              <div className="step-content">
                <h3>To ti khon</h3>
                <p>ng k mt ti khon min ph  bt u s dng ng dng v truy cp cc tnh nng</p>
              </div>
            </div>
            
            <div className="step-connector"></div>
            
            <div className="step-item">
              <div className="step-number">2</div>
              <div className="step-content">
                <h3>Cp nht thng tin</h3>
                <p>Nhp thng tin c nhn, lch trnh thuc v k hoch iu tr ca bn</p>
              </div>
            </div>
            
            <div className="step-connector"></div>
            
            <div className="step-item">
              <div className="step-number">3</div>
              <div className="step-content">
                <h3>Bt u qun l</h3>
                <p>Nhn thng bo, theo di tin trnh v duy tr kt ni vi i ng y t ca bn</p>
              </div>
            </div>
          </div>
          
          <div className="cta-container">
            <Link to="/register" className="cta-button">
              ng k ngay
              <i className="fas fa-user-plus"></i>
            </Link>
          </div>
        </section>
        
        {/* Resources Section */}
        <section className="resources-section">
          <div className="section-header">
            <h2>Ti nguyn hu ch</h2>
            <p>Khm ph cc thng tin gio dc v ti liu h tr v HIV/AIDS</p>
          </div>
          
          <div className="resources-grid">
            <div className="resource-item">
              <div className="resource-icon">
                <i className="fas fa-info-circle"></i>
              </div>
              <h3>Thng tin v HIV</h3>
              <p>Tm hiu thm v HIV, cch ly truyn, phng nga v iu tr</p>
              <Link to="/resources" className="resource-link">Khm ph</Link>
            </div>
            
            <div className="resource-item">
              <div className="resource-icon">
                <i className="fas fa-pills"></i>
              </div>
              <h3>Thng tin v thuc ARV</h3>
              <p>Tm hiu v cc loi thuc khng virus, cch dng v tc dng ph</p>
              <Link to="/resources" className="resource-link">Khm ph</Link>
            </div>
            
            <div className="resource-item">
              <div className="resource-icon">
                <i className="fas fa-users"></i>
              </div>
              <h3>Nhm h tr</h3>
              <p>Kt ni vi cng ng v nhm h tr cho ngi sng vi HIV</p>
              <Link to="/resources" className="resource-link">Khm ph</Link>
            </div>
            
            <div className="resource-item">
              <div className="resource-icon">
                <i className="fas fa-question-circle"></i>
              </div>
              <h3>Cu hi thng gp</h3>
              <p>Tm cu tr li cho cc cu hi thng gp v HIV v vic iu tr</p>
              <Link to="/resources" className="resource-link">Khm ph</Link>
            </div>
          </div>
        </section>
        
        {/* Testimonials Section */}
        <section className="testimonials-section">
          <div className="section-header">
            <h2>Chia s t ngi dng</h2>
            <p>Nhng g mi ngi ni v ng dng ca chng ti</p>
          </div>
          
          <div className="testimonials-container">
            <div className="testimonial-card">
              <div className="testimonial-content">
                <p>"ng dng ny  thay i cch ti qun l vic iu tr HIV. Ti khng bao gi qun ung thuc v lun cp nht v tnh trng sc khe ca mnh."</p>
              </div>
              <div className="testimonial-author">
                <div className="author-avatar">
                  <img src="/testimonial-1.jpg" alt="User Avatar" />
                </div>
                <div className="author-info">
                  <h4>Nguyn V.A</h4>
                  <p>ang s dng ng dng 1 nm</p>
                </div>
              </div>
            </div>
            
            <div className="testimonial-card">
              <div className="testimonial-content">
                <p>"Rt d s dng v hu ch. Cc nhc nh ung thuc v lch hn  gip ti duy tr vic iu tr mt cch u n."</p>
              </div>
              <div className="testimonial-author">
                <div className="author-avatar">
                  <img src="/testimonial-2.jpg" alt="User Avatar" />
                </div>
                <div className="author-info">
                  <h4>Trn T.B</h4>
                  <p>ang s dng ng dng 6 thng</p>
                </div>
              </div>
            </div>
          </div>
        </section>
        
        {/* Call to Action Section */}
        <section className="cta-section">
          <div className="cta-content">
            <h2>Bt u qun l vic iu tr HIV ca bn ngay hm nay</h2>
            <p>ng k min ph v tri nghim cc tnh nng ton din m ng dng ca chng ti cung cp</p>
            
            <div className="cta-buttons">
              <Link to="/register" className="primary-button large">
                ng k ngay
                <i className="fas fa-arrow-right"></i>
              </Link>
              <Link to="/resources" className="text-button">
                Tm hiu thm
                <i className="fas fa-info-circle"></i>
              </Link>
            </div>
          </div>
        </section>
        
        {/* Footer */}
        <footer className="home-footer">
          <div className="footer-content">
            <div className="footer-logo">
              <img src="/hospital-logo.png" alt="HIV Treatment App Logo" />
              <h3>HIV Treatment App</h3>
            </div>
            
            <div className="footer-links">
              <div className="footer-links-column">
                <h4>Trang ch</h4>
                <ul>
                  <li><Link to="/">Trang ch</Link></li>
                  <li><Link to="/hospital">Bnh vin</Link></li>
                  <li><Link to="/hiv-department">Khoa HIV</Link></li>
                  <li><Link to="/resources">Ti nguyn</Link></li>
                </ul>
              </div>
              
              <div className="footer-links-column">
                <h4>Tnh nng</h4>
                <ul>
                  <li><Link to="/medication">Qun l thuc</Link></li>
                  <li><Link to="/appointments">t lch khm</Link></li>
                  <li><Link to="/treatment-plan">K hoch iu tr</Link></li>
                  <li><Link to="/profile">H s c nhn</Link></li>
                </ul>
              </div>
              
              <div className="footer-links-column">
                <h4>H tr</h4>
                <ul>
                  <li><Link to="/resources">Cu hi thng gp</Link></li>
                  <li><Link to="/resources">Hng dn s dng</Link></li>
                  <li><a href="mailto:support@hivhospital.vn">Lin h h tr</a></li>
                  <li><Link to="/resources">Nhm h tr</Link></li>
                </ul>
              </div>
              
              <div className="footer-links-column">
                <h4>Php l</h4>
                <ul>
                  <li><Link to="#">iu khon s dng</Link></li>
                  <li><Link to="#">Chnh sch bo mt</Link></li>
                  <li><Link to="#">Quyn ring t</Link></li>
                  <li><Link to="#">Cookies</Link></li>
                </ul>
              </div>
            </div>
          </div>
          
          <div className="footer-bottom">
            <div className="social-links">
              <a href="#" className="social-link"><i className="fab fa-facebook-f"></i></a>
              <a href="#" className="social-link"><i className="fab fa-twitter"></i></a>
              <a href="#" className="social-link"><i className="fab fa-instagram"></i></a>
              <a href="#" className="social-link"><i className="fab fa-youtube"></i></a>
            </div>
            
            <div className="copyright">
              <p>&copy; 2025 HIV Treatment App. All rights reserved.</p>
            </div>
          </div>
        </footer>
      </div>
    </>
  );
};

export default Home;