import React, { useState } from 'react';
import { Link, useNavigate, useLocation } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';
import Navbar from '../components/Navbar';
import '../styles/Login.css';

const Login = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [rememberMe, setRememberMe] = useState(false);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [formError, setFormError] = useState('');
  
  const { login, error } = useAuth();
  const navigate = useNavigate();
  const location = useLocation();
  
  // Ly ng dn redirect t state (nu c), mc nh l "/"
  const redirectPath = location.state?.from?.pathname || "/";

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (!email || !password) {
      setFormError('Vui lng nhp y  email v mt khu');
      return;
    }
    
    setIsSubmitting(true);
    setFormError('');
    
    try {
      const success = await login(email, password);
      if (success) {
        // Chuyn v trang trc  hoc trang ch
        navigate(redirectPath, { replace: true });
      } else {
        setFormError(error || 'ng nhp tht bi');
      }
    } catch (err) {
      setFormError(' xy ra li khi ng nhp');
      console.error(err);
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <>
      <Navbar />
      <div className="login-container">
        <div className="login-card">
          <div className="login-header">
            <h2>ng nhp</h2>
            <p>Vui lng nhp thng tin ng nhp ca bn  tip tc</p>
          </div>
          
          {formError && <div className="login-error">{formError}</div>}
          
          <form onSubmit={handleSubmit} className="login-form">
            <div className="form-group">
              <label htmlFor="email">Email</label>
              <div className="input-with-icon">
                <i className="fas fa-envelope"></i>
                <input
                  type="email"
                  id="email"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                  placeholder="Nhp a ch email"
                  required
                />
              </div>
            </div>
            
            <div className="form-group">
              <label htmlFor="password">Mt khu</label>
              <div className="input-with-icon">
                <i className="fas fa-lock"></i>
                <input
                  type="password"
                  id="password"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  placeholder="Nhp mt khu"
                  required
                />
              </div>
            </div>
            
            <div className="form-options">
              <div className="remember-me">
                <input
                  type="checkbox"
                  id="remember"
                  checked={rememberMe}
                  onChange={(e) => setRememberMe(e.target.checked)}
                />
                <label htmlFor="remember">Ghi nh ng nhp</label>
              </div>
              <Link to="/forgot-password" className="forgot-link">Qun mt khu?</Link>
            </div>
            
            <button 
              type="submit" 
              className="login-button" 
              disabled={isSubmitting}
            >
              {isSubmitting ? 'ang x l...' : 'ng nhp'}
            </button>
          </form>
          
          <div className="login-divider">
            <span>Hoc ng nhp vi</span>
          </div>
          
          <div className="social-login">
            <button className="google-btn">
              <i className="fab fa-google"></i>
              Google
            </button>
            <button className="facebook-btn">
              <i className="fab fa-facebook-f"></i>
              Facebook
            </button>
          </div>
          
          <div className="login-footer">
            <p>Cha c ti khon? <Link to="/register">ng k ngay</Link></p>
          </div>
        </div>
      </div>
    </>
  );
};

export default Login;